import {
  Component,
  OnInit,
  ViewChild,
  Output,
  Input,
  EventEmitter,
  TemplateRef,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DatabaseSelection } from 'src/app/types/database-selection.model';
import { ConnectionService } from '../../../services/connection.service';
import { GlobalsService } from '../../../services/globals.service';
import { MessageService } from '../../../services/message.service';

import { InputToolsService } from 'src/app/services/input-tools.service';
import { SearchBaseComponent } from '../base/search/search-base.component';
import { AddressService } from 'src/app/services/address.service';
import { SearchResultContact } from 'src/app/types/search-result-contact.model';

@Component({
  selector: 'app-search-contact',
  templateUrl: './search-contact.component.html',
  styleUrls: ['../base/search/search-base.component.scss'],
})
export class SearchContactComponent
  extends SearchBaseComponent
  implements OnInit
{
  @ViewChild('searchContact', { static: true }) modalTemp: TemplateRef<any>;

  @Input() data: any;

  @Output() messageEvent = new EventEmitter<TemplateRef<any>>();

  public searchResults: SearchResultContact[];

  constructor(
    translate: TranslateService,
    connection: ConnectionService,
    globals: GlobalsService,
    messageService: MessageService,
    inputService: InputToolsService,
    addressService: AddressService
  ) {
    super(
      translate,
      connection,
      globals,
      messageService,
      inputService,
      addressService
    );
    this.prefix = 'Contacts: ';
    this.path = 'search';
    this.searchResults = [];
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  getTemplate(): TemplateRef<any> {
    return this.modalTemp;
  }

  filterForContacts(resp: Record<string, any>) {
    console.log(resp);
    const contactDBs: Record<string, any> = {};

    for (const alias in resp) {
      if (alias === 'GeOfficeAliases') {
        continue;
      }
      if (resp[alias].type === 'Contact') {
        contactDBs[alias] = resp[alias].title;
      }
    }

    this.generateSelectionArr(contactDBs);
  }

  generateSelectionArr(dbs: Record<string, any>) {
    for (const db in dbs) {
      if (Object.prototype.hasOwnProperty.call(dbs, db)) {
        const newContact: DatabaseSelection = {
          value: db,
          viewValue: this.prefix + dbs[db],
        };
        console.log(newContact);
        this.databases.push(newContact);
      }
    }

    if (this.databases.length > 0) {
      this.selectedDB = this.databases[0].value;
      this.fetchSearch(this.selectedDB);
    }
    console.log(this.databases);
  }

  fetchDBs() {
    this.connection.getReqModal('dbConnections', null).subscribe((resp) => {
      this.filterForContacts(resp);
    });
  }

  handleResult(resp: any) {
    if (
      resp.result != null &&
      Array.isArray(resp.result) &&
      resp.result.length > 0
    ) {
      this.searchResults = resp.result as SearchResultContact[];
    } else {
      this.searchResults = [];
    }
    console.log(this.searchResults);
  }

  confirmAction() {
    console.log(this.selection.selected[0].value.fdMe);
    if (this.selection.isEmpty()) {
      this.translate.get('LC53493').subscribe((text) => {
        this.messageService.showError(text);
      });
    } else {
      this.openAnotherModal(this.next, {
        currentContactPointer: this.selection.selected[0].value.fdMe,
        createType: this.data.createType,
        contactsDB: this.selectedDB,
      });
    }
  }
}
