import {
  OnInit,
  Directive,
  AfterViewInit,
  Output,
  Input,
  EventEmitter,
  TemplateRef,
} from '@angular/core';
import { ModalInterface } from 'src/app/types/modal-interface';
import { TranslateService } from '@ngx-translate/core';
import { ModalComponent } from '../modal.component';
import { ModalType } from 'src/app/types/modal.type';

@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class ModalBaseComponent
  implements OnInit, AfterViewInit, ModalInterface
{
  @Input() data: any;
  @Input() modalMain: ModalComponent;

  @Output() messageEvent = new EventEmitter<TemplateRef<any>>();

  // TODO: Draggable https://material.angular.io/cdk/drag-drop/api

  public modalHeader: TemplateRef<any>;
  public modalFooter: TemplateRef<any>;

  protected modalTemp: TemplateRef<any>;
  protected modalTitle: string;

  private nextModal: ModalType | null;
  private previousModal: ModalType | null;

  constructor(public translate: TranslateService) {
    this.nextModal = null;
    this.previousModal = null;

    translate.setDefaultLang('all');
    translate.use('all');
  }

  ngOnInit(): void {
    this.next = this.data.nextType;
  }

  ngAfterViewInit() {
    this.messageEvent.emit(this.modalTemp);
    console.log(this.data);
    this.modalHeader = this.data.modalHeader;
    this.modalFooter = this.data.modalFooter;
  }

  getTemplate(): TemplateRef<any> {
    return this.modalTemp;
  }

  extractDBFromPointer(pointer: string) {
    return pointer.substring(pointer.lastIndexOf('|') + 1);
  }

  confirmAction() {
    // Empty, to be implemented by extending class
  }

  cancelAction() {
    // Placeholder, to be implemented by extending class
    this.data.closeAllModal();
  }

  closeAll() {
    this.data.closeAllModal();
  }

  get next(): ModalType | null {
    return this.nextModal;
  }

  set next(modal: ModalType | null) {
    this.nextModal = modal;
  }

  get previous(): ModalType | null {
    return this.previousModal;
  }

  set previous(modal: ModalType | null) {
    this.previousModal = modal;
  }

  set title(text: string) {
    this.modalTitle = text;
  }

  protected openAnotherModal(
    modalToOpen: ModalType | null,
    passToNextComponent?: Record<string, any>
  ) {
    if (modalToOpen != null) {
      return this.modalMain.loadComponent(modalToOpen, passToNextComponent);
    }
    return null;
  }
}
