import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subscriber } from 'rxjs';
import { AuthService } from 'src/app/graph/services/auth.service';
import { ConnectionService } from '../connection.service';
import { GlobalsService } from '../globals.service';
import { MessageService } from '../message.service';
import { WaitListService } from '../waitList.service';
import { ConnectorOWAService } from './connector-owa.service';
import { ConnectorVerseService } from './connector-verse.service';
import { ConnectorService } from './connector.service';

@Injectable({
  providedIn: 'root',
})
export class ConnectorFactory {
  constructor(
    public translate: TranslateService,
    private globals: GlobalsService,
    private message: MessageService,
    private auth: AuthService,
    private waitlist: WaitListService,
    private connection: ConnectionService
  ) {}

  // Checks which app accesses BM
  public checkForApp(): Observable<any> {
    console.log('FACTORY');
    return new Observable<ConnectorService>((observer) => {
      try {
        Office.onReady(() => {
          console.log('FACTORY OWA');
          observer.next(
            new ConnectorOWAService(
              this.globals,
              this.message,
              this.auth,
              this.waitlist
            )
          );
          observer.complete();
        });
      } catch (e) {
        if (sessionStorage.getItem('verseAppData') != null) {
          const verse = new ConnectorVerseService(
            this.translate,
            this.globals,
            this.message,
            this.connection
          );
          verse.data = JSON.parse(
            sessionStorage.getItem('verseAppData') as string
          );
          sessionStorage.clear();
          observer.next(verse);
          observer.complete();
        } else {
          window.addEventListener(
            'message',
            async (event) => {
              this.verseHandler(event, observer);
            },
            false
          );
        }
      }
    });
  }

  private async verseHandler(
    event: any,
    observer: Subscriber<ConnectorService>
  ) {
    sessionStorage.clear();
    console.log('INIT VERSE');
    console.log(event);
    if (event.data.verseApiType === 'com.ibm.verse.ping.application.loaded') {
      const loadedMessage = {
        verseApiType: 'com.ibm.verse.application.loaded',
      };
      if (
        event.source != null &&
        !(event.source instanceof MessagePort) &&
        !(event.source instanceof ServiceWorker)
      ) {
        console.log(event.source);
        event.source.postMessage(loadedMessage, event.origin);
      }
    }
    if (event.data.verseApiType === 'com.ibm.verse.action.clicked') {
      const verse = new ConnectorVerseService(
        this.translate,
        this.globals,
        this.message,
        this.connection
      );

      verse.data = event.data.verseApiData;

      observer.next(verse);
      observer.complete();
    }
  }
}
