import { HttpParams } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ConnectionService } from 'src/app/services/connection.service';
import { CreateAddressService } from 'src/app/services/create-address.service';
import { GlobalsService } from 'src/app/services/globals.service';
import {
  ConfigIcons,
  ConfigIconType,
  ConfigIconValues,
} from 'src/app/types/config-icons.model';
import {
  iconDefaultConfig,
  ConfigData,
} from '../../otherClasses/config-items-default.class';

@Component({
  selector: 'app-configuration-actions',
  templateUrl: './configuration-actions.component.html',
  styleUrls: ['./configuration-actions.component.scss'],
})
export class ConfigurationActionsComponent implements OnInit {
  public configIcon: { [klass: string]: any } | null;
  public iconConfigLocal = iconDefaultConfig;
  public configData: Record<ConfigIconType, ConfigData>;

  private path = 'config';
  private configRestObj: Record<string, any>;

  constructor(
    private connectionService: ConnectionService,
    private createService: CreateAddressService,
    public globals: GlobalsService
  ) {
    this.configIcon = {
      background:
        'transparent url(assets/images/icons_business_mail.png) no-repeat',
    };
    this.configRestObj = {};
    this.createService.config = this.iconConfigLocal;
  }

  ngOnInit(): void {
    this.initConfig();
  }

  public toggleClass(configTypeIndex: number, callOpenToggle: boolean = true) {
    console.log(this.configData);
    if (configTypeIndex < this.iconConfigLocal.length) {
      let curObj = this.iconConfigLocal[configTypeIndex].model;
      const objKeysArr = Object.keys(curObj.classAssignment);
      curObj = this.changeIcon(curObj, objKeysArr);
      if (
        this.configData &&
        this.configData[curObj.name] &&
        this.configData[curObj.name].inRest
      ) {
        this.configRestObj[curObj.name] = curObj.value;
        let params: HttpParams = new HttpParams();
        for (const key of Object.keys(this.configRestObj)) {
          params = params.set(key, this.configRestObj[key]);
        }
        if (configTypeIndex === 1) {
          // Open Mail
          this.globals.openMail = Boolean(curObj.value);
        } else if (configTypeIndex === 0) {
          // Delete Attachments
          this.globals.deleteAttachments = Boolean(curObj.value);
        }
        this.connectionService
          .postReq(this.path, params, false, undefined, true)
          .subscribe({
            next: (resp: any) => {
              // Do nothing
            },
            error: (errHttp: any) => {
              // TODO Errorhandling
              console.log(errHttp);
            },
            complete: () => {
              // Do nothing
            },
          });
      } else if (this.configData && this.configData.createAddress) {
        if (callOpenToggle) {
          this.createService.toggleOpen();
        }
      }
    }
  }

  private changeIcon(curObj: ConfigIcons, objKeysArr: string[]) {
    curObj.value = objKeysArr[
      (objKeysArr.indexOf(curObj.value) + 1) % objKeysArr.length
    ] as ConfigIconValues;
    return curObj;
  }

  private initConfig() {
    console.log(this.connectionService);
    this.connectionService.getReqNoCache(this.path, null).subscribe({
      next: (resp: any) => {
        console.log(resp);
        this.initConfigClasses(resp);
      },
      error: (errHttp: any) => {
        this.connectionService.processPath().subscribe(() => {
          //this.initConfig();
        });
      },
      complete: () => {
        // Do nothing
      },
    });
  }

  private initConfigClasses(resp: any) {
    this.configRestObj = resp;
    const tmpConfigData: any = {};

    let forIndex = 0;

    for (const ele of this.iconConfigLocal) {
      const eleName = ele.model.name;
      let currentConfig;

      if (!currentConfig) {
        currentConfig = {
          inRest: false,
          index: 0,
        };
      } else {
        currentConfig = this.configData[eleName];
      }

      if (resp[eleName] || resp[eleName] === '') {
        ele.model.value = resp[eleName];
        currentConfig.inRest = true;
        if (eleName === 'deleteAttachments' || eleName === 'openMail') {
          this.globals[eleName] = Boolean(resp[eleName]);
        }
      }

      console.log('RESP:' + resp);
      console.log(resp);

      tmpConfigData[eleName] = currentConfig;

      currentConfig.index = forIndex;
      forIndex++;
    }

    this.configData = tmpConfigData;
  }
}
