/* eslint-disable @typescript-eslint/naming-convention */
import { Injectable } from '@angular/core';
import { HttpHeaders, HttpParams } from '@angular/common/http';
import { WebintInputService } from './webint-input.service';
import { WaitListService } from './waitList.service';
import { Observable, Subscriber } from 'rxjs';
import { ConnectorTypesAlias } from '../types/connector.type';
import { ConnectorService } from './connectors/connector.service';

@Injectable({
  providedIn: 'root',
})
export class GlobalsService {
  readonly apiOverviewPath = 'restBM.xsp/verserest/';

  private webIntRelativeStart: string;
  private currentWebInt: string;
  private webIntUrl: URL;
  private mailAddr: string;
  private mailUnid: string;
  private optionsObj: { [key: string]: any };
  private lcs: Record<string, string>;
  private lcsLocal: Record<string, string>;
  private rippleColorRGBA: string;
  private createAddressOpen: boolean;
  private subject: string;
  private cc: Record<string, string>[];
  private from: Record<string, string>;
  private to: Record<string, string>[];
  private mailAddrCount: number;
  private ownMail: string;
  private resetMailAddr: string;
  private showMailBody: boolean;
  private connectorType: ConnectorTypesAlias;
  private connector: ConnectorService;
  private graphReq: boolean;
  private graphLoad: boolean;
  private openEMail: boolean;
  private deleteAttach: boolean;
  private allowActions: boolean;
  private allowCreate: boolean;

  private loginWindowOpen: boolean;

  // Use cache while modal is open - get current result if new (first) modal is opened
  private modalSessionCache: boolean;

  // Modal globals (Temporary - as long as modal is opened)
  private selectedContacts: string | null;

  constructor(private waitListService: WaitListService) {
    this.currentWebInt = '';
    if (window.localStorage.getItem('webInt') != null) {
      this.currentWebInt = window.localStorage.getItem('webInt') as string;
    }
    this.mailAddr = '';
    this.mailUnid = '';
    this.subject = '';
    this.resetMailAddr = '';
    this.cc = [];
    this.to = [];
    this.from = {};
    this.mailAddrCount = 0;
    if (this.currentWebInt) {
      this.webIntUrl = new URL(this.currentWebInt);
    }
    this.webIntRelativeStart = this.webInt + this.apiOverviewPath;
    this.lcs = {};
    this.lcsLocal = {};
    this.selectedContacts = null;
    this.modalSessionCache = false;
    this.rippleColorRGBA = 'rgba(154, 169, 255, 0.1)';
    this.createAddressOpen = false;
    this.ownMail = '';
    this.showMailBody = true;
    this.connectorType = null;
    this.allowActions = false;
    this.allowCreate = false;

    this.optionsObj = {
      method: 'GET',
      mode: 'cors',
      cache: 'no-cache',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: 'follow',
      referrerPolicy: 'no-referrer',
    };
  }

  public set loginOpen(val: boolean) {
    this.loginWindowOpen = val;
  }

  public get loginOpen() {
    return this.loginWindowOpen;
  }

  public set actionsAllowed(val: boolean) {
    this.allowActions = val;
  }

  public get actionsAllowed() {
    return this.allowActions;
  }

  public set createAllowed(val: boolean) {
    this.allowCreate = val;
  }

  public get createAllowed() {
    return this.allowCreate;
  }

  public set openMail(val: boolean) {
    this.openEMail = val;
  }

  public get openMail() {
    return this.openEMail;
  }

  public set deleteAttachments(val: boolean) {
    this.deleteAttach = val;
  }

  public get deleteAttachments() {
    return this.deleteAttach;
  }

  public set curConnectorType(val: ConnectorTypesAlias) {
    this.connectorType = val;
  }

  public get curConnectorType() {
    return this.connectorType;
  }

  public set curConnector(val: ConnectorService) {
    this.connector = val;
  }

  public get curConnector() {
    return this.connector;
  }

  public set graphRequired(val: boolean) {
    this.graphReq = val;
    for (const entry of this.waitListService.accessWaitList('graphRequired')) {
      entry.next(val);
      entry.complete();
    }
  }

  public get graphRequired() {
    return this.graphReq;
  }

  public set graphLoaded(val: boolean) {
    this.graphLoad = val;
    for (const entry of this.waitListService.accessWaitList('graphLoaded')) {
      entry.next(val);
      entry.complete();
    }
  }

  public get graphLoaded() {
    return this.graphLoad;
  }

  public get isCreateBlockOpen() {
    return this.createAddressOpen;
  }

  public get rippleColor() {
    return this.rippleColorRGBA;
  }

  public set webInt(value: string) {
    this.currentWebInt = value;
    this.webIntUrl = new URL(this.currentWebInt);
    if (this.currentWebInt != null && this.currentWebInt !== '') {
      this.notifyWaitList();
    }
  }

  public get webInt() {
    return this.currentWebInt;
  }

  public set displayMailBody(val: boolean) {
    this.showMailBody = val;
  }

  public get displayMailBody() {
    return this.showMailBody;
  }

  public set curMailAddr(value: string) {
    if (this.resetMailAddr === '') {
      this.resetMailAddr = value;
    }
    this.mailAddr = value;
    this.waitListService.resolveFuncWaitList('mailAddr');
  }

  public get curMailAddr() {
    return this.mailAddr;
  }

  public get startMailAddr() {
    return this.resetMailAddr;
  }

  public set curSubject(value: string) {
    this.subject = value;
  }

  public get curSubject() {
    return this.subject;
  }

  public set curCC(value: Record<string, string>[]) {
    this.cc = value;
  }

  public get curCC() {
    return this.cc;
  }

  public set curTo(value: Record<string, string>[]) {
    this.to = value;
  }

  public get curTo() {
    return this.to;
  }

  public set curFrom(value: Record<string, string>) {
    this.from = value;
  }

  public get curFrom() {
    return this.from;
  }

  public set curMailAddrCount(value: number) {
    this.mailAddrCount = value;
  }

  public get curMailAddrCount() {
    return this.mailAddrCount;
  }

  public set curMailUnid(value: string) {
    this.mailUnid = value;
  }

  public get curMailUnid() {
    return this.mailUnid;
  }

  public get origin() {
    return this.webIntUrl.origin;
  }

  public get requestWebInt() {
    return this.webIntRelativeStart;
  }

  public get options() {
    return this.optionsObj;
  }

  public get languageConf() {
    return this.lcs;
  }

  public set languageConf(newLCs: Record<string, string>) {
    this.lcs = newLCs;
  }

  public set ownMailAddr(mail: string) {
    this.ownMail = mail;
  }

  public waitForlanguageConfLocal() {
    return new Observable((observer) => {
      if (this.lcsLocal !== {}) {
        observer.next(this.lcsLocal);
        observer.complete();
      } else {
        this.waitListService.addToWaitList('localLC', observer);
      }
    });
  }

  public set languageConfLocal(newLCs: Record<string, string>) {
    this.lcsLocal = newLCs;
  }

  public get languageConfLocal() {
    return this.lcsLocal;
  }

  public get currentContactsDB() {
    return this.selectedContacts;
  }

  public set currentContactsDB(newContactsDB: string | null) {
    this.selectedContacts = newContactsDB;
  }

  public get modalCacheFlag() {
    return this.modalSessionCache;
  }

  public set modalCacheFlag(flag: boolean) {
    this.modalSessionCache = flag;
  }

  public languageEntry(key: string) {
    return this.lcs[key];
  }

  public changeGlobalOptions(newObj: { [key: string]: any }): {
    [key: string]: any;
  } {
    return this.changeOptionsObj(newObj, this.optionsObj);
  }

  public changeOwnOptions(newObj: { [key: string]: any }): {
    [key: string]: any;
  } {
    const ownOptions: { [key: string]: any } = {};
    Object.assign(ownOptions, this.optionsObj);

    return this.changeOptionsObj(newObj, ownOptions);
  }

  public objectWithParams(params: HttpParams) {
    let newObject: Record<string, any> = {};
    newObject = Object.assign(newObject, this.options);
    newObject.params = params;
    return newObject;
  }

  public clearTemps() {
    this.clearModalTemps();
  }

  public clearModalTemps() {
    this.currentContactsDB = null;
  }

  public createAddressToggle() {
    this.createAddressOpen = !this.createAddressOpen;
  }

  public waitForWebInt(observer: Subscriber<boolean>) {
    this.waitListService.addToWaitList('webInt', observer);
  }

  private notifyWaitList() {
    for (const sub of this.waitListService.accessWaitList('webInt')) {
      sub.next(this.webInt);
      sub.complete();
    }
    this.waitListService.clearWaitList('webInt');
  }

  private changeOptionsObj(
    newObj: { [key: string]: any },
    optionsToChange: { [key: string]: any }
  ): { [key: string]: any } {
    const map: Map<string, any> = new Map(Object.entries(newObj));
    map.forEach((value: any, key: string) => {
      optionsToChange[key] = value;
    });
    return optionsToChange;
  }
}
