import { Injectable } from '@angular/core';
import { Observable, Subscriber } from 'rxjs';
import { GlobalsService } from './globals.service';
import CreateAddressItems from '../otherClasses/create-address-items.class';
import { iconDefaultConfig } from '../otherClasses/config-items-default.class';

@Injectable({
  providedIn: 'root',
})
export class CreateAddressService {
  private createInstance: CreateAddressItems;
  private subscribers: Subscriber<boolean>[];
  private open: boolean;
  private configData = iconDefaultConfig;

  constructor(private globals: GlobalsService) {
    this.subscribers = [];
    this.createInstance = new CreateAddressItems();
    this.open = false;
  }

  public connect() {
    return new Observable<boolean>((observer) => {
      this.subscribers.push(observer);
      observer.next(this.globals.isCreateBlockOpen);
    });
  }

  public updateObservers() {
    const curState = this.globals.isCreateBlockOpen;
    for (const sub of this.subscribers) {
      sub.next(curState);
    }
  }

  public toggleOpen() {
    this.open = !this.open;
    if (this.isOpen) {
      this.configData[2].model.value = '1';
    } else {
      this.configData[2].model.value = '';
    }
    this.globals.createAddressToggle();
    this.updateObservers();
  }

  get items() {
    return this.createInstance.items;
  }

  get isOpen() {
    return this.open;
  }

  set config(data: typeof iconDefaultConfig) {
    this.configData = data;
  }
}
