import { Component, HostListener, OnInit } from '@angular/core';
import { ModalItem } from 'src/app/otherClasses/modal-item';
import { ConnectorFactory } from 'src/app/services/connectors/connector-factory.service';
import { ConnectorService } from 'src/app/services/connectors/connector.service';
import { GlobalsService } from 'src/app/services/globals.service';
import { InitService } from 'src/app/services/init.service';
import { ChangeableCSSNames } from 'src/app/types/changeable-css-names.type';

import { ModalService } from '../../services/modal.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  public title = 'BusinessMail';
  public modalItems: Record<string, ModalItem>;

  public init: boolean;

  public newCSS: Record<ChangeableCSSNames, { [klass: string]: any }> = {
    main: {},
    overflowContainer: {},
  };

  private connector: ConnectorService;

  constructor(
    private modalService: ModalService,
    private initService: InitService,
    private connectorFactory: ConnectorFactory,
    private globals: GlobalsService
  ) {
    this.isInit = false;
  }

  ngOnInit() {
    this.connectorFactory.checkForApp().subscribe((connectorClass) => {
      this.connector = connectorClass;
      this.initConnectorData();
    });
    this.modalService.waitForModalDesign().subscribe((resp) => {
      this.initModalItems();
    });
    this.initService.waitForInit().subscribe((resp) => {
      this.isInit = resp;
    });
    window.addEventListener('keydown', this.ownReload);
  }

  public initModalItems() {
    this.modalItems = this.modalService.getModalItems();
    console.log(this.modalItems);
    this.isInit = true;
  }

  private initConnectorData() {
    this.connector.waitForCSS().subscribe((resp) => {
      this.newCSS = resp;
    });
    this.connector.getData();
  }

  private ownReload(e: any) {
    if ((e.which || e.keyCode) === 116) {
      if (this.globals.curConnectorType === 'Owa') {
        window.location.reload();
      } else {
        sessionStorage.setItem(
          'verseAppData',
          JSON.stringify(this.globals.curConnector.data)
        );
        const curURL = window.location.href;
        if (!window.location.href.includes('index.html')) {
          let urlAddition = 'index.html';
          if (!window.location.href.endsWith('/')) {
            urlAddition = '/' + urlAddition;
          }
          window.location.href += urlAddition;
        }
      }
    }
  }

  get isInit() {
    return this.init;
  }

  set isInit(newVal: boolean) {
    this.init = newVal;
  }

  get mItems() {
    return this.modalItems;
  }
}
