/* eslint-disable max-len */
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConnectionService } from './connection.service';
import { GlobalsService } from './globals.service';
import { LoaderService } from './loader.service';
import { MessageService } from './message.service';

@Injectable({
  providedIn: 'root',
})
export class SoapService {
  constructor(
    public translate: TranslateService,
    private connection: ConnectionService,
    private globals: GlobalsService,
    private messageService: MessageService,
    private loaderService: LoaderService
  ) {}

  // Calls SOAP method
  // Parameters need to be passed as an object
  public async invokeSoap(
    method: any,
    parameters: any,
    firstAsArray?: boolean
  ) {
    this.loaderService.activateLoader(false);
    if (parameters !== null && typeof parameters === 'object') {
      try {
        let data =
          '<soapenv:Envelope xmlns:soapenv="http://schemas.xmlsoap.org/soap/envelope/" xmlns:soapenc="http://schemas.xmlsoap.org/soap/encoding/"  xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:urn="urn:DefaultNamespace">' +
          '<soapenv:Header/>' +
          '<soapenv:Body>' +
          '<urn:' +
          method +
          '>';

        for (const key in parameters) {
          if (firstAsArray) {
            data +=
              '<' +
              key +
              ' soapenc:arrayType="xsd:string[1]">' +
              '<item>' +
              '<![CDATA[' +
              parameters[key] +
              ']]>' +
              '</item>' +
              '</' +
              key +
              '>';
            firstAsArray = false;
          } else {
            data +=
              '<' + key + '><![CDATA[' + parameters[key] + ']]></' + key + '>';
          }
        }

        data +=
          '</urn:' + method + '>' + '</soapenv:Body>' + '</soapenv:Envelope>';

        const response = await this.connection.postSoap(
          this.globals.webInt + '/businessMailContacts',
          data
        );
        let responseText;

        if (response.status === 200) {
          responseText = await response.text();
        } else {
          throw new Error('');
        }

        const xmlDoc = new window.DOMParser().parseFromString(
          responseText,
          'text/xml'
        );

        const xmlReturnEle = xmlDoc.getElementsByTagName(method + 'Return')[0];

        const result = [];
        let child;
        if (xmlDoc.childNodes.length > 0) {
          // eslint-disable-next-line @typescript-eslint/prefer-for-of
          for (let i = 0; i < xmlReturnEle.childNodes.length; i++) {
            child = xmlReturnEle.childNodes[i];
            result.push(child.textContent ? child.textContent : '');
          }
        } else {
          result.push(xmlDoc.textContent ? xmlDoc.textContent : '');
        }
        this.loaderService.deactivateLoader();
        return this.arrayToObj(result, '~#~');
      } catch (e) {
        // Failed to retrieve data
        this.translate.get('LC53488').subscribe((text) => {
          this.messageService.showError(text);
          this.loaderService.deactivateLoader();
          throw Error(text);
        });
      }
    } else {
      // Invalid data
      this.translate.get('LC53487').subscribe((text) => {
        this.messageService.showError(text);
        throw Error(text);
      });
    }
    this.loaderService.deactivateLoader();
  }

  private arrayToObj(arr: any, devider: any) {
    const obj: any = {};
    for (const index in arr) {
      if (Object.prototype.hasOwnProperty.call(arr, index)) {
        const splitArr = arr[index].split(devider);
        if (splitArr.length >= 2) {
          obj[splitArr[0]] = splitArr[1];
        } else if (splitArr.length === 1) {
          obj[splitArr[0]] = '';
        }
      }
    }
    return obj;
  }
}
