import {
  Component,
  OnInit,
  ViewChild,
  Input,
  TemplateRef,
  ElementRef,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { ModalBaseComponent } from '../base/modal-base.component';
import {
  createLabels,
  startLabels,
} from '../../../types/create-label-names.type';
import { MessageService } from 'src/app/services/message.service';
import { MatSelectionList } from '@angular/material/list';
import { SoapService } from 'src/app/services/soap.service';
import { CreateAddressHandlerService } from 'src/app/services/create-address-handler.service';
import { GlobalsService } from 'src/app/services/globals.service';

const MAX_ROWS = 23;

@Component({
  selector: 'app-create-contact',
  templateUrl: './create-contact.component.html',
  styleUrls: ['./create-contact.component.scss'],
})
export class CreateContactComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('create', { static: true }) modalTemp: TemplateRef<any>;
  @ViewChild('labels') selection: MatSelectionList;
  @ViewChild('areaData') areaData: ElementRef;
  @ViewChild('previewData') previewData: ElementRef;

  @Input() data: any;

  public keyLabels: string[];
  public currentKeyLabels: string[];
  public fieldDisplayNameMap: Map<string, string>;

  private fieldDisplayNameList: string[];

  constructor(
    public translate: TranslateService,
    public globals: GlobalsService,
    private messageService: MessageService,
    private soapService: SoapService,
    private createHandler: CreateAddressHandlerService
  ) {
    super(translate);
    this.fieldDisplayNameList = [];
    this.fieldDisplayNameMap = new Map<string, string>();
    this.setTranslatedNameList();
    this.keyLabels = createLabels;
    this.currentKeyLabels = startLabels;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.processScanFooter().then((resp) => {
      this.fillTextArea(resp);
      console.log(resp);
      console.log(this.areaData);
    });
  }

  getTemplate(): TemplateRef<any> {
    return this.modalTemp;
  }

  public changeLabel(selectedKey: string, index: number) {
    const newModal = this.openAnotherModal('createLabel', {
      labelDisplayMap: this.fieldDisplayNameMap,
      createLabels,
      selected: selectedKey,
    });

    if (newModal) {
      newModal.afterClosed().subscribe((resp) => {
        if (resp) {
          this.currentKeyLabels[index] = resp;
        }
      });
    }
  }

  // Calls SOAP ScanFooter
  async processScanFooter() {
    console.log('SCAN');
    console.log(this.globals.curConnector.curPostData);
    const mailContent = this.globals.curConnector.curPostData.preview;
    console.log('MAIL DATA');
    console.log(mailContent);

    let footerData;
    try {
      footerData = await this.soapService.invokeSoap('scanMailFooter', {
        mailContent,
      });
    } catch (e) {
      // Nothing
    }

    return footerData;
  }

  public confirmAction() {
    console.log('CONFIRM ACTION');
    console.log(arguments);
    let value = '';
    if (Array.isArray(arguments[0])) {
      value = arguments[0][0].value;
    } else {
      value = arguments[0].value;
    }
    this.createAddress(value);
  }

  private fillPreviewDisplay() {
    console.log('Fill Preview');
    console.log(this.globals.displayMailBody);
    console.log(this.previewData);
    console.log(this.areaData);
    if (this.globals.displayMailBody && this.previewData) {
      console.log('Fill Preview If');
      console.log(this.globals.curConnector.preview);
      this.previewData.nativeElement.textContent =
        this.globals.curConnector.preview;
    }
  }

  private createAddress(textContent: string) {
    console.log('CREATE METHOD!');
    console.log(textContent);
    this.createHandler.create(
      textContent,
      this.currentKeyLabels,
      this.fieldDisplayNameMap,
      this.data.passedData.contactsDB,
      MAX_ROWS,
      this.data.passedData.createType,
      this.data.closeAllModal,
      this.data.passedData.currentContactPointer
    );
  }

  private async fillTextArea(resp: any) {
    let text = '';
    let first = true;
    const mail = this.globals.curMailAddr;

    if (this.data.passedData.currentContactPointer) {
      resp = await this.createHandler.replaceWithCompanyData(
        '',
        this.currentKeyLabels,
        this.fieldDisplayNameMap,
        MAX_ROWS,
        this.data.passedData.currentContactPointer,
        resp
      );
      console.log(resp);
    }

    for (const index in this.currentKeyLabels) {
      if (Object.prototype.hasOwnProperty.call(this.currentKeyLabels, index)) {
        if (!first) {
          text += '\n';
        } else {
          first = false;
        }
        let curVal = resp[this.currentKeyLabels[index]];
        if (this.currentKeyLabels[index] === 'EMailAddress') {
          if (mail != null && (curVal == null || curVal === '')) {
            if (Array.isArray(mail) && mail.length > 0) {
              // Assign first E Mail Address
              curVal = mail[0];
            } else {
              curVal = mail;
            }
          }
        }
        if (curVal == null) {
          curVal = '';
        }
        text += curVal.trim();
        this.areaData.nativeElement.textContent = text;
      }
    }
    this.fillPreviewDisplay();
  }

  private setTranslatedNameList() {
    // List of translated keys for contact
    this.translate.get('LC73020').subscribe((text) => {
      this.fieldDisplayNameList = text.split(';');
      console.log(this.fieldDisplayNameList);
      console.log(createLabels);
      this.createDisplayMap(this.fieldDisplayNameList);
    });
  }

  private createDisplayMap(values: string[]) {
    console.log(createLabels.length + 1);
    console.log(values.length);
    if (createLabels.length === values.length) {
      // Index 0 of translation not used in keys
      let valueIndex = 1;
      for (const labelKey of createLabels) {
        if (labelKey === '') {
          continue;
        }
        this.fieldDisplayNameMap.set(labelKey, values[valueIndex]);
        valueIndex++;
      }
      this.fieldDisplayNameMap.set('', this.fieldDisplayNameList[0]);
    } else {
      this.translate.get('LC53488').subscribe((text) => {
        this.messageService.showError(text);
      });
    }
  }
}
