import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ConnectionService } from 'src/app/services/connection.service';
import { GlobalsService } from 'src/app/services/globals.service';
import { InputToolsService } from 'src/app/services/input-tools.service';
import { LoaderService } from 'src/app/services/loader.service';
import { MessageService } from 'src/app/services/message.service';

@Component({
  selector: 'app-webint-input',
  templateUrl: './webint-input.component.html',
  styleUrls: ['./webint-input.component.scss'],
})
export class WebintInputComponent implements OnInit {
  @ViewChild('webInp', { static: true }) modalTemp: TemplateRef<any>;

  public contactDBs: Record<string, string>[];
  public modCloseImage: { [klass: string]: any } | null;
  public lcLocal;
  public inputValue: string;

  constructor(
    private messageService: MessageService,
    private sanitize: InputToolsService,
    private loader: LoaderService,
    public connectionService: ConnectionService,
    public modal: MatDialog,
    public globals: GlobalsService
  ) {
    this.modCloseImage = {
      background: 'no-repeat url(assets/images/widget_settings.png)',
    };
    this.lcLocal = this.globals.languageConfLocal;
    this.inputValue = '';
    if (window.localStorage.getItem('webInt') != null) {
      this.connectionService.webIntModalOpenRef = this.open.bind(this);
    }
  }

  ngOnInit(): void {
    if (window.localStorage.getItem('webInt') != null) {
      this.inputValue = window.localStorage.getItem('webInt') as string;
    } else {
      this.loader.activateLoader(true);
      setTimeout(() => {
        this.loader.forceDeactivateLoader();
        this.connectionService.webIntModalOpenRef = this.open.bind(this);
      }, 2000);
    }
  }

  open() {
    return this.modal.open(this.modalTemp, {
      panelClass: 'modalOverlay',
      autoFocus: false,
      maxHeight: '90%',
    });
  }

  confirmAction() {
    this.checkInput(this.sanitize.sanitizeInput(this.inputValue, 4));
    console.log(this.inputValue);
  }

  closeModal() {
    this.modal.closeAll();
  }

  private async checkInput(inputVal: string | null) {
    console.log(inputVal);
    if (inputVal && inputVal.includes('.nsf')) {
      inputVal = inputVal.substring(0, inputVal.indexOf('.nsf') + 4);
    } else {
      inputVal = null;
    }
    if (inputVal === this.globals.webInt) {
      this.closeModal();
      return;
    }
    if (inputVal == null || inputVal === '') {
      this.messageService.showError(
        this.globals.languageConfLocal.webIntInvalid
      );
    } else if (inputVal.includes('http:')) {
      this.messageService.showWarning(
        this.globals.languageConfLocal.webIntHttp
      );
    } else {
      if (!inputVal.startsWith('https')) {
        inputVal = 'https://' + inputVal;
      }
      if (!inputVal.endsWith('/')) {
        inputVal = inputVal + '/';
      }
      if (await this.connectionService.checkWebInt(inputVal)) {
        this.connectionService.connect().subscribe(() => {
          this.connectionService.reload();
        });
      }
    }
  }
}
