import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { WaitListService } from './waitList.service';

@Injectable({
  providedIn: 'root',
})
export class InitService {
  private isInit: boolean;

  constructor(private waitList: WaitListService) {
    this.isInit = false;
  }

  set initComplete(newVal: boolean) {
    this.isInit = newVal;
    if (newVal) {
      this.informWaitList();
    }
  }

  get initComplete() {
    return this.isInit;
  }

  public waitForInit() {
    return new Observable<boolean>((observer) => {
      observer.next(this.isInit);
      if (!this.isInit) {
        this.waitList.addToWaitList('init', observer);
      } else {
        observer.complete();
        this.informWaitList();
      }
    });
  }

  private informWaitList() {
    const subs = this.waitList.accessWaitList('init');
    for (const sub of subs) {
      sub.next(this.initComplete);
      if (this.initComplete) {
        sub.complete();
      }
    }
    this.waitList.clearWaitList('init');
  }
}
