/* eslint-disable no-underscore-dangle */
import { Component, OnInit, Renderer2 } from '@angular/core';
import { GlobalsService } from '../../services/globals.service';
import { ConnectionService } from 'src/app/services/connection.service';
import { AuthService } from 'src/app/graph/services/auth.service';
import { LoaderService } from 'src/app/services/loader.service';

import { HttpParams } from '@angular/common/http';
import { CreateAddressService } from 'src/app/services/create-address.service';
import {
  ConfigIcons,
  ConfigIconType,
  ConfigIconValues,
} from 'src/app/types/config-icons.model';
import {
  iconDefaultConfig,
  ConfigData,
} from '../../otherClasses/config-items-default.class';

@Component({
  selector: 'app-site-actions',
  templateUrl: './site-actions.component.html',
  styleUrls: ['./site-actions.component.scss'],
})
export class SiteActionsComponent implements OnInit {
  // Includes actions for Refreshing App and Login / Logout
  enteredButton = false;
  isMatMenuOpen = false;
  isMatMenu2Open = false;
  loggedIn = false;
  prevButtonTrigger: any;
  userName: string;
  reloadImg: any;

  public configIcon: { [klass: string]: any } | null;
  public iconConfigLocal = iconDefaultConfig;
  public configData: Record<ConfigIconType, ConfigData>;

  private path = 'config';
  private configRestObj: Record<string, any>;

  constructor(
    public globals: GlobalsService,
    public connectionService: ConnectionService,
    private createService: CreateAddressService,
    private ren: Renderer2,
    private authService: AuthService,
    private loader: LoaderService
  ) {
    this.userName = 'Login';
    this.reloadImg = {
      background: 'no-repeat url(assets/images/icon_refresh_no_background.png)',
    };
    this.configIcon = {
      background:
        'transparent url(assets/images/icons_business_mail.png) no-repeat',
    };
    this.configRestObj = {};
    this.createService.config = this.iconConfigLocal;
  }

  ngOnInit(): void {
    this.waitForUserName();
    if (window.localStorage.getItem('webInt') === null) {
      this.openWebIntModal();
    }
    this.initConfig();
  }

  menuEnter() {
    this.isMatMenuOpen = true;
    if (this.isMatMenu2Open) {
      this.isMatMenu2Open = false;
    }
  }

  menuLeave(trigger: any, firstEle: any) {
    setTimeout(() => {
      if (!this.isMatMenu2Open && !this.enteredButton) {
        this.isMatMenuOpen = false;
        trigger.closeMenu();
        if (firstEle) {
          this.ren.removeClass(
            firstEle._elementRef.nativeElement,
            'cdk-focused'
          );
          this.ren.removeClass(
            firstEle._elementRef.nativeElement,
            'cdk-program-focused'
          );
        }
      } else {
        this.isMatMenuOpen = false;
      }
    }, 80);
  }

  buttonEnter(trigger: any, firstEle: any) {
    setTimeout(() => {
      if (this.prevButtonTrigger && this.prevButtonTrigger !== trigger) {
        this.prevButtonTrigger.closeMenu();
        this.prevButtonTrigger = trigger;
        this.isMatMenuOpen = false;
        this.isMatMenu2Open = false;
        trigger.openMenu();
      } else if (!this.isMatMenuOpen) {
        this.enteredButton = true;
        this.prevButtonTrigger = trigger;
        trigger.openMenu();
        if (firstEle) {
          this.ren.removeClass(
            firstEle._elementRef.nativeElement,
            'cdk-focused'
          );
          this.ren.removeClass(
            firstEle._elementRef.nativeElement,
            'cdk-program-focused'
          );
        }
      } else {
        this.enteredButton = true;
        this.prevButtonTrigger = trigger;
      }
    });
  }

  buttonLeave(trigger: any) {
    setTimeout(() => {
      if (this.enteredButton && !this.isMatMenuOpen) {
        trigger.closeMenu();
      }
      if (!this.isMatMenuOpen) {
        trigger.closeMenu();
      } else {
        this.enteredButton = false;
      }
    }, 100);
  }

  reload() {
    this.connectionService.reload();
  }

  openWebIntModal() {
    this.connectionService.waitForWebIntModal().subscribe((openModal: any) => {
      openModal();
    });
  }

  openConnectWindow() {
    if (this.userName === 'Login') {
      if (window.localStorage.getItem('webInt') === null) {
        this.openWebIntModal();
      } else {
        this.connectionService.connect().subscribe(() => {
          // Connection is handled by service
        });
      }
    }
  }

  openGraphLogin() {
    this.authService.signIn();
  }

  public toggleClass(configTypeIndex: number, callOpenToggle: boolean = true) {
    console.log(this.configData);
    if (configTypeIndex < this.iconConfigLocal.length) {
      let curObj = this.iconConfigLocal[configTypeIndex].model;
      const objKeysArr = Object.keys(curObj.classAssignment);
      curObj = this.changeIcon(curObj, objKeysArr);
      if (
        this.configData &&
        this.configData[curObj.name] &&
        this.configData[curObj.name].inRest
      ) {
        this.configRestObj[curObj.name] = curObj.value;
        let params: HttpParams = new HttpParams();
        for (const key of Object.keys(this.configRestObj)) {
          params = params.set(key, this.configRestObj[key]);
        }
        if (configTypeIndex === 1) {
          // Open Mail
          this.globals.openMail = Boolean(curObj.value);
        } else if (configTypeIndex === 0) {
          // Delete Attachments
          this.globals.deleteAttachments = Boolean(curObj.value);
        }
        this.connectionService
          .postReq(this.path, params, false, undefined, true)
          .subscribe({
            next: (resp: any) => {
              // Do nothing
            },
            error: (errHttp: any) => {
              // TODO Errorhandling
              console.log(errHttp);
            },
            complete: () => {
              // Do nothing
            },
          });
      } else if (this.configData && this.configData.createAddress) {
        if (callOpenToggle) {
          this.createService.toggleOpen();
        }
      }
    }
  }

  private waitForUserName() {
    this.connectionService.processPath().subscribe(() => {
      this.connectionService.getUserName().subscribe((resp: any) => {
        this.globals.ownMailAddr = resp.mail;
        this.userName = resp.name;
        this.loggedIn = true;
      });
    });
  }

  private changeIcon(curObj: ConfigIcons, objKeysArr: string[]) {
    curObj.value = objKeysArr[
      (objKeysArr.indexOf(curObj.value) + 1) % objKeysArr.length
    ] as ConfigIconValues;
    return curObj;
  }

  private initConfig() {
    console.log(this.connectionService);
    this.connectionService.getReqNoCache(this.path, null).subscribe({
      next: (resp: any) => {
        console.log(resp);
        this.initConfigClasses(resp);
      },
      error: (errHttp: any) => {
        this.connectionService.processPath().subscribe(() => {
          //this.initConfig();
        });
      },
      complete: () => {
        // Do nothing
      },
    });
  }

  private initConfigClasses(resp: any) {
    this.configRestObj = resp;
    const tmpConfigData: any = {};

    let forIndex = 0;

    for (const ele of this.iconConfigLocal) {
      const eleName = ele.model.name;
      let currentConfig;

      if (!currentConfig) {
        currentConfig = {
          inRest: false,
          index: 0,
        };
      } else {
        currentConfig = this.configData[eleName];
      }

      if (resp[eleName] || resp[eleName] === '') {
        ele.model.value = resp[eleName];
        currentConfig.inRest = true;
        if (eleName === 'deleteAttachments' || eleName === 'openMail') {
          this.globals[eleName] = Boolean(resp[eleName]);
        }
      }

      console.log('RESP:' + resp);
      console.log(resp);

      tmpConfigData[eleName] = currentConfig;

      currentConfig.index = forIndex;
      forIndex++;
    }

    this.configData = tmpConfigData;
  }
}
