import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ConnectionService } from './connection.service';
import { GlobalsService } from './globals.service';
import { WaitListService } from './waitList.service';

@Injectable({
  providedIn: 'root',
})
export class LanguageService {
  private loaded: boolean;

  constructor(
    private global: GlobalsService,
    private waitList: WaitListService,
    private http: HttpClient,
    private globals: GlobalsService,
    public connectionService: ConnectionService
  ) {
    this.loaded = false;
    connectionService.waitForLC().subscribe(() => {
      this.loaded = true;
      this.processLCWaitingList();
    });
    this.setLCSLocal();
  }

  public waitForInit() {
    return new Observable<boolean>((observer) => {
      if (this.loaded) {
        observer.next(true);
        observer.complete();
        this.processLCWaitingList();
      } else {
        this.waitList.addToWaitList('lc', observer);
      }
    });
  }

  public getLCEntry(lc: number, toReplace?: string, replaceString?: string) {
    return this.replacePlaceholder(
      this.global.languageEntry('LC' + lc),
      toReplace,
      replaceString
    );
  }

  public passLCObj() {
    return this.global.languageConf;
  }

  public getLang() {
    const userLang = navigator.language.toLowerCase();
    if (userLang.startsWith('de')) {
      return 'de';
    } else {
      return 'en';
    }
  }

  public setLCSLocal() {
    this.http
      .get('assets/i18n/' + this.getLang() + '.json')
      .subscribe((resp) => {
        this.globals.languageConfLocal = resp as Record<string, string>;
        this.localLCReady();
      });
  }

  public localLCReady() {
    for (const sub of this.waitList.accessWaitList('localLC')) {
      sub.next(this.globals.languageConfLocal);
      sub.complete();
    }
  }

  private processLCWaitingList() {
    console.log('------ Process wait list');
    this.waitList.accessWaitList('lc').forEach((observer) => {
      observer.next();
      observer.complete();
    });
  }

  private replacePlaceholder(
    text: string,
    toReplace?: string,
    replaceString?: string
  ) {
    if (toReplace && replaceString) {
      text = text.replace(toReplace, replaceString);
    }
    return text;
  }
}
