import {
  Component,
  OnInit,
  ViewChild,
  TemplateRef,
  OnDestroy,
} from '@angular/core';

import { ModalBaseComponent } from '../base/modal-base.component';
import { TranslateService } from '@ngx-translate/core';
import { MatSelectionList } from '@angular/material/list';

import { MessageService } from '../../../services/message.service';
import { GlobalsService } from '../../../services/globals.service';

@Component({
  selector: 'app-select-create-label',
  templateUrl: './select-create-label.component.html',
  styleUrls: ['./select-create-label.component.css'],
})
export class SelectCreateLabelComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('selLabelCreate', { static: true }) modalTemp: TemplateRef<any>;
  @ViewChild('selection') selection: MatSelectionList;

  public selectedEntry;

  constructor(
    public translate: TranslateService,
    private messageService: MessageService,
    private globals: GlobalsService
  ) {
    super(translate);
    this.selectedEntry = '';
  }

  ngOnInit(): void {
    super.ngOnInit();
    console.log(this.data.passedData);
    console.log(this.data.passedData.createLabels.indexOf(''));
    if (this.data.passedData.createLabels.indexOf('') === -1) {
      this.data.passedData.createLabels.unshift('');
    }
    this.selectedEntry = this.data.passedData.selected;
  }

  confirmAction() {
    this.data.closeModal(this.selection.selectedOptions.selected[0].value);
  }
}
