import {
  OnInit,
  Output,
  Input,
  EventEmitter,
  TemplateRef,
  Directive,
  ViewChild,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SelectionModel } from '@angular/cdk/collections';
import { DatabaseSelection } from 'src/app/types/database-selection.model';
import { ConnectionService } from '../../../../services/connection.service';
import { GlobalsService } from '../../../../services/globals.service';
import { MessageService } from '../../../../services/message.service';

import { ModalBaseComponent } from '../modal-base.component';
import { InputToolsService } from 'src/app/services/input-tools.service';
import { AddressService } from 'src/app/services/address.service';

@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class SearchBaseComponent
  extends ModalBaseComponent
  implements OnInit
{
  @Input() data: any;

  @Output() messageEvent = new EventEmitter<TemplateRef<any>>();

  public selectedDB: string;
  public searchValue: string;
  public databases: DatabaseSelection[];
  public viewIconBaseURL: string;
  public displayedColumns: string[];
  public selection = new SelectionModel<any>(false);
  public searchClearImage: { [klass: string]: any } | null;
  public ngTmpName: string;

  centered = false;
  disabled = false;
  unbounded = false;

  protected prefix: string;
  protected path: string;

  constructor(
    public translate: TranslateService,
    protected connection: ConnectionService,
    protected globals: GlobalsService,
    protected messageService: MessageService,
    protected inputService: InputToolsService,
    protected addressService: AddressService
  ) {
    super(translate);
    this.databases = [];
    this.searchValue = '';
    this.viewIconBaseURL = globals.webInt;
    this.searchClearImage = {
      background: 'no-repeat url(assets/images/widget_settings.png)',
    };
    this.displayedColumns = ['icon', 'display'];
    this.prefix = '';
    this.path = '';
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.fetchDBs();
  }

  getTemplate(): TemplateRef<any> {
    return this.modalTemp;
  }

  get rippleColor() {
    return this.globals.rippleColor;
  }

  public fetchSearch(database: string | null = this.globals.currentContactsDB) {
    let searchKey = this.searchValue;
    if (searchKey === '') {
      searchKey = '*';
    }
    this.selection.clear();

    const paramObj: Record<string, any> = {
      searchKey,
      database,
    };

    if (this.data.type) {
      paramObj.type = this.data.type;
    }

    if (this.data.parentPointer) {
      paramObj.parentPointer = this.data.parentPointer;
    }

    console.log(paramObj);

    this.connection
      .getReqNoCache(this.path, this.connection.objectToHttpParams(paramObj))
      .subscribe({
        next: (resp: any) => {
          this.handleResult(resp);
        },
        error: (errHttp: any) => {
          // TODO Errorhandling
          console.log(errHttp);
        },
        complete: () => {
          // Do nothing
        },
      });
  }

  public toggleSelection(row: DatabaseSelection) {
    this.selection.toggle(row);
  }

  public search() {
    let input: string | null = this.inputService.sanitizeInput(
      this.searchValue,
      4
    );
    if (input === null) {
      this.translate.get('LC53513').subscribe((text) => {
        this.messageService.showError(text);
      });
      return;
    } else {
      if (input === '') {
        input = '*';
      } else {
        this.searchValue = input;
      }
      this.fetchSearch(this.selectedDB);
    }
  }

  public acceptEntryDblClick(row: DatabaseSelection) {
    this.selection.clear();
    this.selection.select(row);
    this.confirmAction();
  }

  abstract handleResult(resp: any): void;
  abstract confirmAction(): void;
  abstract generateSelectionArr(dbs: Record<string, any>): void;
  abstract fetchDBs(): void;
  /*this.connection.getReqModal('dbConnections', null).subscribe((resp) => {
      this.filterForContacts(resp);
    });*/
}
