import { KeyValue } from '@angular/common';

export const order = [
  'name',
  'companyName',
  'birthdate',
  '-BLANK-',
  'phone',
  'cellphone',
  'homephone',
  '-BLANK-',
  'homepage',
  'skype',
  'pointer',
  'companyPointer',
  'isCompany',
  'additional',
];

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function compareAddressOrder(
  key1: KeyValue<string, any>,
  key2: KeyValue<string, any>
) {
  const indexKey1 = order.indexOf(key1.key);
  const indexKey2 = order.indexOf(key2.key);
  if (indexKey1 === -1 && indexKey2 === -1) {
    return 0;
  }
  if (indexKey1 === -1) {
    return 1;
  }
  if (indexKey2 === -1) {
    return -1;
  }
  if (indexKey1 < indexKey2) {
    return -1;
  }
  if (indexKey1 > indexKey2) {
    return 1;
  }
  return 0;
}

export const spacerIndices = ((searchType: string) => {
  const indicies: number[] = [];
  order.forEach((element, index) => {
    if (element.includes(searchType)) {
      indicies.push(index);
    }
  });
  return indicies;
})('-BLANK-');
