// <MSALFactorySnippet>
let msalInstance: IPublicClientApplication | undefined;

export function MSALInstanceFactory(): IPublicClientApplication {
  // TODO https://github.com/AzureAD/microsoft-authentication-library-for-js/issues/1403
  // TODO Wenns nicht funktioniert, asset daten lschen

  msalInstance =
    msalInstance ??
    new PublicClientApplication({
      auth: {
        clientId: OAuthSettings.appId,
        redirectUri: OAuthSettings.redirectUri,
        postLogoutRedirectUri: OAuthSettings.redirectUri,
      },
      cache: {
        cacheLocation: BrowserCacheLocation.LocalStorage,
      },
    });

  return msalInstance;
}
// </MSALFactorySnippet>

