import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ChangeableCSSNames } from 'src/app/types/changeable-css-names.type';
import { ConnectorTypesAlias } from 'src/app/types/connector.type';
import { DocumentPost } from 'src/app/types/document-post-interface';
import { DocumentedTypes } from 'src/app/types/documented.type';
import { GlobalsService } from '../globals.service';

@Injectable({
  providedIn: 'root',
})
export abstract class ConnectorService {
  public postData: DocumentPost;
  protected appData: any;

  constructor(protected globals: GlobalsService) {}

  public waitForConnectorType(): Observable<ConnectorTypesAlias> {
    return new Observable<ConnectorTypesAlias>((observer) => {
      if (this.globals.curConnectorType != null) {
        observer.next(this.globals.curConnectorType);
        observer.complete();
      }
    });
  }

  public get preview() {
    return this.postData.preview;
  }

  public abstract waitForCSS(): Observable<
    Record<ChangeableCSSNames, { [klass: string]: any }>
  >;

  public abstract getData(): void;

  public abstract checkMailType(): void;

  protected abstract adjustToastPosition(): void;

  protected abstract getPostData(): Promise<any>;

  public abstract set data(newVal: any);

  public abstract get data(): any;

  public abstract get curPostData(): any;

  public abstract get isMailAlreadyDoc(): boolean;

  public abstract set alreadyDocumented(val: Record<DocumentedTypes, boolean>);
}
