import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { LoaderService } from '../../services/loader.service';

@Component({
  selector: 'app-loader',
  templateUrl: './loader.component.html',
  styleUrls: ['./loader.component.scss'],
})
export class LoaderComponent implements OnInit, OnDestroy {
  public loaderActive: boolean;
  public isModal: boolean;
  private loaderSubscription: Subscription;

  constructor(public loader: LoaderService) {
    this.loaderActive = false;
    this.isModal = false;
  }

  ngOnInit(): void {
    this.loaderSubscription = this.loader
      .connectToService()
      .subscribe((resp) => {
        this.changeState(resp.activate, resp.modal);
      });
  }

  ngOnDestroy(): void {
    console.log('DESTROYED');
  }

  private changeState(activeState: boolean, isModal = this.isModal) {
    console.log(activeState + ' ' + isModal);
    // ExpressionChangedAfterItHasBeenCheckedError Fix
    setTimeout(() => {
      this.loaderActive = activeState;
      this.isModal = isModal;
    }, 1);
  }
}
