import {
  Component,
  OnInit,
  ViewChild,
  Output,
  Input,
  EventEmitter,
  TemplateRef,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DatabaseSelection } from 'src/app/types/database-selection.model';
import { SearchResultTemplate } from 'src/app/types/search-result-template.model';
import { ConnectionService } from '../../../services/connection.service';
import { GlobalsService } from '../../../services/globals.service';
import { MessageService } from '../../../services/message.service';

import { InputToolsService } from 'src/app/services/input-tools.service';
import { SearchBaseComponent } from '../base/search/search-base.component';
import { AddressService } from 'src/app/services/address.service';
import { HttpParams } from '@angular/common/http';

@Component({
  selector: 'app-search-template',
  templateUrl: './search-template.component.html',
  styleUrls: ['../base/search/search-base.component.scss'],
})
export class SearchTemplateComponent
  extends SearchBaseComponent
  implements OnInit
{
  @ViewChild('searchTemplate', { static: true }) modalTemp: TemplateRef<any>;

  @Input() data: any;

  @Output() messageEvent = new EventEmitter<TemplateRef<any>>();

  public searchResults: SearchResultTemplate[];

  constructor(
    translate: TranslateService,
    connection: ConnectionService,
    globals: GlobalsService,
    messageService: MessageService,
    inputService: InputToolsService,
    addressService: AddressService
  ) {
    super(
      translate,
      connection,
      globals,
      messageService,
      inputService,
      addressService
    );
    this.prefix = 'Template: ';
    this.path = 'templates';
    this.searchResults = [];
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  getTemplate(): TemplateRef<any> {
    return this.modalTemp;
  }

  createPhoneCallHttpParams(templatePointer: string) {
    const templateType = 'PhoneCall';
    const createDocumentType = 'faActivities';

    let params = new HttpParams();
    params = params.appendAll({
      createDocument: createDocumentType,
      parentPointer: this.addressService.currentPointer,
      templatePointer,
      templateType,
    });

    if (this.globals.curSubject != null) {
      params = params.set('cval-Subject', this.globals.curSubject);
    }

    const paramsToAdd = ['crmDB'];

    return {
      paramsToAdd,
      params,
    };
  }

  convertToArr(resp: Record<string, any>) {
    const templateDBs: Record<string, any> = {};

    console.log(resp);

    for (const index in resp) {
      if (resp[index].type === 'Template') {
        templateDBs[resp[index].alias] = resp[index].title;
      }
    }

    this.generateSelectionArr(templateDBs);
  }

  generateSelectionArr(dbs: Record<string, any>) {
    console.log('???');
    console.log(dbs);
    for (const db in dbs) {
      if (Object.prototype.hasOwnProperty.call(dbs, db)) {
        const newTemplate: DatabaseSelection = {
          value: db,
          viewValue: this.prefix + dbs[db],
        };
        console.log(newTemplate);
        this.databases.push(newTemplate);
      }
    }

    this.data.parentPointer = this.addressService.currentPointer;

    if (this.databases.length > 0) {
      console.log(this.databases);
      this.selectedDB = this.databases[0].value;
      this.fetchSearch(this.selectedDB);
    }
    console.log(this.databases);
  }

  fetchDBs() {
    this.connection
      .getReqModal(
        'templates',
        this.connection.objectToHttpParams({
          templateDBs: 'true',
        })
      )
      .subscribe((resp) => {
        console.log(resp);
        this.convertToArr(resp);
      });
  }

  handleResult(resp: any) {
    console.log(resp);
    if (resp != null && Array.isArray(resp) && resp.length > 0) {
      this.searchResults = resp as SearchResultTemplate[];
    } else {
      this.searchResults = [];
    }
    console.log(this.searchResults);
  }

  confirmAction() {
    if (this.selection.isEmpty()) {
      this.translate.get('LC53493').subscribe((text) => {
        this.messageService.showError(text);
      });
    } else {
      this.openAnotherModal(this.next, {
        paramsData: this.createPhoneCallHttpParams(
          this.selection.selected[0].pointer
        ),
        ignoreNext: true,
      });
    }
  }
}
