import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { ModalBaseComponent } from '../base/modal-base.component';
import { TranslateService } from '@ngx-translate/core';
import { MatSelectionList } from '@angular/material/list';

import { MessageService } from '../../../services/message.service';
import { GlobalsService } from 'src/app/services/globals.service';
import { ReloadService } from 'src/app/services/reload.service';

@Component({
  selector: 'app-select-mail-address',
  templateUrl: './select-mail-address.component.html',
  styleUrls: ['./select-mail-address.component.scss'],
})
export class SelectMailAddressComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('selMailAddr', { static: true }) modalTemp: TemplateRef<any>;
  @ViewChild('mailAddrs') selection: MatSelectionList;

  constructor(
    public globals: GlobalsService,
    public translate: TranslateService,
    private messageService: MessageService,
    private reloadService: ReloadService
  ) {
    super(translate);
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  public confirmAction() {
    try {
      this.globals.curMailAddr =
        this.selection.selectedOptions.selected[0].value;
      this.closeAll();
      this.reloadService.reload();
    } catch (e) {
      this.translate.get('LC53493').subscribe((text) => {
        this.messageService.showError(text);
      });
    }
  }
}
