import { Component, OnInit } from '@angular/core';
import { InitService } from 'src/app/services/init.service';
import { Address } from 'src/app/types/address.model';

import { AddressService } from '../../services/address.service';
import { MessageService } from '../../services/message.service';

@Component({
  selector: 'app-pager',
  templateUrl: './pager.component.html',
  styleUrls: ['./pager.component.scss'],
})
export class PagerComponent implements OnInit {
  public appIsInit = false;

  private endIndex = 0;

  constructor(
    private addressService: AddressService,
    private messageService: MessageService,
    private initService: InitService
  ) {}

  ngOnInit(): void {
    this.waitForAddresses();
    this.initService.waitForInit().subscribe((resp) => {
      this.appIsInit = resp;
    });
    this.addressService.waitForAddressChange().subscribe(() => {
      this.waitForAddresses();
    });
  }

  // TODO: On Keypress ArrowKeys
  public changeIndex(changeVal: number) {
    const newIndex: number = this.index + changeVal;
    if (newIndex >= 0 && newIndex <= this.maxIndex) {
      this.addressService.addressIndex = newIndex;
      this.messageService.clearToasts();
    }
  }

  public init(addresses: Address[]) {
    if (addresses.length < 1) {
      this.endIndex = 0;
      return;
    }
    this.endIndex = addresses.length - 1;
    console.log('init');
  }

  private waitForAddresses(): void {
    console.log('-----PAGER-----');
    this.addressService.processAddresses().subscribe({
      next: (addresses: any) => {
        this.init(addresses);
      },
      error: () => {
        // Nothing
      },
      complete: () => {
        // Nothing
      },
    });
  }

  get index() {
    return this.addressService.addressIndex;
  }

  get maxIndex() {
    return this.endIndex;
  }
}
