import { HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { GlobalsService } from './globals.service';

@Injectable({
  providedIn: 'root',
})
export class WebintLinkService {
  private indexParamStart = 'index.xsp?';
  private urlExtension = this.indexParamStart + 'loadTabs=false';

  constructor(private globalsService: GlobalsService) {}

  public convertToLink(pointer: string) {
    return (
      this.globalsService.webInt +
      this.urlExtension +
      '&openDocument=' +
      pointer
    );
  }

  public openWebIntWithParams(params: HttpParams) {
    params = params.set('loadTabs', 'false');
    window.open(
      this.globalsService.webInt + this.indexParamStart + params.toString(),
      '_blank'
    );
  }
}
