import { Injectable, TemplateRef } from '@angular/core';
import { Observable, Subject } from 'rxjs';

import { WaitListService } from './waitList.service';
import { GlobalsService } from './globals.service';

import { ModalType } from '../types/modal.type';
import { ModalItem } from '../otherClasses/modal-item';

import { ChooseOfficeComponent } from '../components/modal/choose-office/choose-office.component';
import { SearchContactComponent } from '../components/modal/search-contact/search-contact.component';
import { CreateContactComponent } from '../components/modal/create-contact/create-contact.component';
import { CreateTicketComponent } from '../components/modal/create-ticket/create-ticket.component';
import { DocumentFileComponent } from '../components/modal/document-file/document-file.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { PassableModalData } from '../types/passable-modal-data';
import { SelectContactComponent } from '../components/modal/select-contact/select-contact.component';
import { SelectCreateLabelComponent } from '../components/modal/select-create-label/select-create-label.component';
import { DuplicateComponent } from '../components/modal/duplicate/duplicate.component';
import { SearchTemplateComponent } from '../components/modal/search-template/search-template.component';
import { SelectMailAddressComponent } from '../components/modal/select-mail-address/select-mail-address.component';

@Injectable({
  providedIn: 'root',
})
export class ModalService {
  public currentType: Observable<{
    newType: ModalType;
    data?: Record<string, any>;
  }>;

  private modalTypeSource = new Subject<{
    newType: ModalType;
    data?: Record<string, any>;
  }>();
  private modalHeader: TemplateRef<any>;
  private modalFooter: TemplateRef<any>;
  private activeModals: MatDialogRef<any, any>[];
  private modalItems: Record<ModalType, ModalItem>;

  constructor(
    private waitList: WaitListService,
    private globals: GlobalsService,
    private matDialog: MatDialog
  ) {
    this.currentType = this.modalTypeSource.asObservable();
    this.activeModals = [];
  }

  public changeType(objToPass: {
    newType: ModalType;
    data?: PassableModalData;
  }) {
    this.modalTypeSource.next(objToPass);
  }

  public setModalHeader(newHeader: TemplateRef<any>) {
    console.log('++++++++++++++ SETMODALHEADER ++++++++++');
    console.log(newHeader);
    this.modalHeader = newHeader;
  }

  public setModalFooter(newFooter: TemplateRef<any>) {
    console.log('------ SETMODALFOOTER ------');
    console.log(newFooter);
    this.modalFooter = newFooter;
    this.modalItems = this.getModalItems();
    this.waitList.accessWaitList('modalDesign').forEach((observer) => {
      observer.next(this.modalItems);
      observer.complete();
      this.waitList.clearWaitList('modalDesign');
    });
  }

  public waitForModalDesign() {
    return new Observable<any>((observer) => {
      this.waitList.addToWaitList('modalDesign', observer);
    });
  }

  public getModalItems(): Record<ModalType, ModalItem> {
    if (!this.modalItems) {
      return {
        chooseOfficeDB: new ModalItem(
          ChooseOfficeComponent,
          this.initModalData('documentFile')
        ),
        selectContact: new ModalItem(
          SelectContactComponent,
          this.initModalData()
        ),
        searchContact: new ModalItem(
          SearchContactComponent,
          this.initModalData('chooseOfficeDB')
        ),
        searchTemplate: new ModalItem(
          SearchTemplateComponent,
          this.initModalData('chooseOfficeDB')
        ),
        createContact: new ModalItem(
          CreateContactComponent,
          this.initModalData()
        ),
        createTicket: new ModalItem(
          CreateTicketComponent,
          this.initModalData()
        ),
        documentFile: new ModalItem(
          DocumentFileComponent,
          this.initModalData()
        ),
        createAddressCompany: new ModalItem(
          SelectContactComponent,
          this.initModalData('createContact', {
            createType: 'company',
          })
        ),
        createAddressToCompany: new ModalItem(
          SearchContactComponent,
          this.initModalData('createContact', {
            type: 'company',
            createType: 'toCompany',
          })
        ),
        createAddressPerson: new ModalItem(
          SelectContactComponent,
          this.initModalData('createContact', {
            createType: 'person',
          })
        ),
        createLabel: new ModalItem(
          SelectCreateLabelComponent,
          this.initModalData(null, {
            currentModal: this.newActive,
          })
        ),
        selectMailAddress: new ModalItem(
          SelectMailAddressComponent,
          this.initModalData()
        ),
        duplicate: new ModalItem(DuplicateComponent, this.initModalData()),
      };
    } else {
      return this.modalItems;
    }
  }

  public addActiveModal(newModal: MatDialogRef<any, any>) {
    this.globals.modalCacheFlag = true;
    this.activeModals.push(newModal);
  }

  public closeActiveModal(passToCaller?: any) {
    const modalToClose = this.activeModals.pop();
    if (modalToClose) {
      modalToClose.close(passToCaller);
    }
    if (this.activeModals.length < 1) {
      this.globals.modalCacheFlag = false;
      this.globals.clearModalTemps();
    }
  }

  public closeAllModals() {
    this.matDialog.closeAll();
    this.activeModals = [];
  }

  public initWebIntModal() {
    return this.modalHeader;
  }

  get modalItemsPub() {
    return this.modalItems;
  }

  get newActive() {
    return this.activeModals[this.activeModals.length - 1];
  }

  private initModalData(
    nextModalType: ModalType | null = null,
    addToData?: Record<string, any>,
    header = this.modalHeader,
    footer = this.modalFooter
  ): any {
    const objToReturn: Record<string, any> = {
      nextType: nextModalType,
      modalHeader: header,
      modalFooter: footer,
      closeModal: this.closeActiveModal.bind(this),
      closeAllModal: this.closeAllModals.bind(this),
      modalItems: this.modalItems,
    };
    if (addToData) {
      for (const key of Object.keys(addToData)) {
        objToReturn[key] = addToData[key];
      }
    }
    return objToReturn;
  }
}
