import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { GlobalsService } from './globals.service';

const TIMEOUT_IN_MS = 5000;

@Injectable({
  providedIn: 'root',
})
export class MessageService {
  private defaultTitle: string;
  private connectionTimeout: NodeJS.Timeout | null;

  private toastrObj = {
    timeOut: 3000,
    positionClass: 'toast-bottom-center',
    preventDuplicates: true,
    progressBar: true,
    closeButton: true,
    maxOpen: 0,
    newestOnTop: false,
  };

  constructor(private toastr: ToastrService, private globals: GlobalsService) {
    this.defaultTitle = '';
    this.connectionTimeout = null;
  }

  public showError(
    message: string,
    title = this.defaultTitle,
    noTimeout?: boolean
  ) {
    if (noTimeout) {
      const newOptions: any = {};
      Object.assign(newOptions, this.toastrObj);
      newOptions.disableTimeOut = true;
      this.toastr.error(message, title, newOptions);
    } else {
      this.toastr.error(message, title, this.toastrObj);
    }
  }

  public showInfo(message: string, title = this.defaultTitle) {
    this.toastr.info(message, title, this.toastrObj);
  }

  public showSuccess(message: string, title = this.defaultTitle) {
    this.toastr.success(message, title, this.toastrObj);
  }

  public showWarning(message: string, title = this.defaultTitle) {
    this.toastr.warning(message, title, this.toastrObj);
  }

  public showCommon(message: string, title = this.defaultTitle) {
    this.toastr.show(message, title, this.toastrObj);
  }

  public setConnectionTimeout(message: string, title = this.defaultTitle) {
    this.clearConnectionTimeout();
    this.connectionTimeout = setTimeout(() => {
      if (!this.globals.loginOpen) {
        this.showError(message, title, true);
        return;
      }
    }, TIMEOUT_IN_MS);
  }

  public clearConnectionTimeout() {
    if (this.connectionTimeout != null) {
      clearTimeout(this.connectionTimeout);
      this.connectionTimeout = null;
    }
  }

  public clearToasts() {
    this.toastr.clear();
  }

  public changeClass(newClass: string) {
    this.toastrObj.positionClass = newClass;
  }
}
