  public processAddress(params: HttpParams) {
    params = params.set('filterImg', 'true');
    return new Observable<{ [key: string]: any }>((observer) => {
      if (this.pathInfo != null) {
        this.postReq('addresses', params, true).subscribe({
          next: (resp: any) => {
            console.log('ProcessAddress');
            console.log(resp);
            observer.next(resp);
            observer.complete();
          },
          error: (errHttp: any) => {
            console.log('HTTP Error', errHttp);
          },
          complete: () => {
            // Nothing
          },
        });
      }
    });
  }

  public waitForLC() {
    return new Observable<boolean>((observer) => {
      if (this.pathInfo == null) {
        console.log('>>>>> Path null wait for lc');
        this.processPath().subscribe(() => {
          console.log('>>>>> Path get');
          this.processLCHandler(observer);
        });
      } else {
        this.processLCHandler(observer);
      }
    });
  }

  public processLCHandler(observer: Subscriber<boolean>) {
    // For typecheck - will only be called when path is set.
    if (this.pathInfo != null) {
      if (Object.keys(this.globals.languageConf).length > 0) {
        observer.next(true);
        observer.complete();
      } else {
        const http$ = from(
          fetch(
            this.globals.webInt +
              this.globals.apiOverviewPath +
              this.pathInfo.translation.relativePath +
              '/businessmail',
            this.globals.options
          )
        );

        http$.subscribe({
          next: async (resp: any) => {
            console.log('LC LC LC LC LC LC');
            console.log(resp);
            if (resp.status !== 200) {
              observer.error(await resp.text());
            } else {
              this.globals.languageConf = await resp.json();
              console.log(resp.body);
              observer.next(true);
              observer.complete();
            }
          },
          error: (errHttp: any) => {
            console.log('HTTP Error', errHttp);
          },
          complete: () => {
            // Nothing
          },
        });
      }
    }
  }

  public async checkWebInt(inputVal: string): Promise<boolean> {
    return await fetch(inputVal + LOGINPATH, {
      method: 'HEAD',
    })
      .then(() => {
        if (this.globals.webInt) {
          this.loggingOut = true;
          this.logout();
        }
        window.localStorage.setItem('webInt', inputVal);
        this.globals.webInt = inputVal;
        return true;
      })
      .catch(() => {
        this.messageService.showError(
          this.globals.languageConfLocal.webIntCertError + ' ' + inputVal,
          undefined,
          true
        );
        return false;
      });
  }

  public async logoutGraph() {
    await this.authService.signOutPopup();
  }

  public async logout() {
    from(
      fetch(
        this.globals.webInt.substr(0, this.globals.webInt.length - 1) +
          '?logout',
        this.globals.options
      )
    ).subscribe({
      next: () => {
        console.log('LOGGING OUT');
        console.log(this.loggingOut);
        this.reload();
      },
      error: async (err) => {
        console.log('ERROR LOGGING OUT');
        console.log(err);
        const resp = await fetch(
          this.globals.webInt + 'restBM.xsp/verserest/',
          {
            method: 'get',
            credentials: 'include',
            cache: 'no-cache',
            mode: 'cors',
          }
        );

        if (!(resp.status != null && resp.status === 401)) {
          if (resp.type != null && resp.type === 'cors') {
            this.reload();
          }
          this.messageService.showError(
            this.globals.languageConfLocal.webIntLogoutFailed,
            undefined,
            true
          );
        } else {
          this.reload();
        }
      },
    });
  }

  public waitForWebIntModal() {
    return new Observable((observer) => {
      if (this.webIntModalOpen) {
        observer.next(this.webIntModalOpen);
        observer.complete();
      } else {
        this.waitList.addToWaitList('webIntModal', observer);
      }
    });
  }

  public getUserName() {
    return new Observable((observer) => {
      this.getReqNoCache('user', null).subscribe((resp) => {
        observer.next(resp);
        observer.complete();
      });
    });
  }

  public waitForGraphRequired() {
    return new Observable((observer) => {
      this.waitList.addToWaitList('graphRequired', observer);
    });
  }

  get isLoggedIn() {
    return this.loggedIn;
  }

  set webIntModalOpenRef(func: any) {
    this.webIntModalOpen = func;
    for (const sub of this.waitList.accessWaitList('webIntModal')) {
      sub.next(func);
      sub.complete();
    }
  }

  private async openGraphLogin() {
    if (!this.globals.graphLoaded) {
      return this.authService.signIn();
    }
    return null;
  }

  private activateLoader() {
    if (this.globals.webInt && this.globals.webInt !== '') {
      this.loaderService.activateLoader(
        Boolean(this.modal.openDialogs.length).valueOf()
      );
    }
  }

  // Avoids caching to check if user is logged in
  private async checkIfLoggedIn(
    http$: Observable<any>,
    observer: Subscriber<{ [key: string]: any }>
  ) {
    if (this.pathInfo != null) {
      let params = new HttpParams();
      params = params.set('noCache', 'true');

      console.log('CHECK IF LOGGED IN ');

      this.getReqNoCache('keepAlive', null, false).subscribe({
        next: async (resp: any) => {
          console.log('Keep Alive Resp');
          console.log(resp);
          if (resp == null || resp !== 'OK') {
            console.log('check if logged in error');
            this.connect().subscribe(this.connectHandler(http$, observer));
          } else {
            this.processPathWaitingList();
            this.pathFetchInProgress = false;
            observer.next(resp);
            observer.complete();
          }
        },
        error: (error) => {
          console.log('check if logged in error');
          this.connect().subscribe(this.connectHandler(http$, observer));
        },
      });
    }
  }

  private processPathWaitingList() {
    console.log('------ Process wait list');
    this.waitList.accessWaitList('path').forEach((observer) => {
      if (this.pathInfo != null) {
        console.log('------ != Null');
        observer.next(this.pathInfo);
        observer.complete();
      } else {
        console.log('------ != Error');
        observer.error();
      }
    });
  }

