import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { GlobalsService } from 'src/app/services/globals.service';
import { LoaderService } from 'src/app/services/loader.service';
import { ModalBaseComponent } from '../base/modal-base.component';
import { DocumentService } from 'src/app/services/document.service';

@Component({
  selector: 'app-document-file',
  templateUrl: './document-file.component.html',
  styleUrls: ['./document-file.component.css'],
})
export class DocumentFileComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('docFile', { static: true }) modalTemp: TemplateRef<any>;

  public text: string;
  private officeDB: string;
  private pointer: string;

  constructor(
    public translate: TranslateService,
    private globals: GlobalsService,
    private loader: LoaderService,
    private documentService: DocumentService
  ) {
    super(translate);
  }

  ngOnInit(): void {
    console.log(this.data.passedData);
    if (this.data.passedData.currentOfficePointer) {
      this.pointer = this.data.passedData.currentOfficePointer;
      this.officeDB = this.pointer.substring(this.pointer.lastIndexOf('|') + 1);
    } else {
      this.officeDB = this.data.passedData.officeDB;
      this.pointer = this.data.passedData.currentContactPointer;
    }
    this.translate.get('LC73019').subscribe((text) => {
      this.text = text + '...';
    });

    this.confirmAction();
  }

  async confirmAction(): Promise<void> {
    this.loader.activateLoader(true, true);
    const data = this.globals.curConnector.curPostData;
    console.log(data);

    if (data != null) {
      this.documentService.documentHandler(
        data,
        this.pointer,
        this.officeDB,
        this
      );
    }
  }

  cancelAction() {
    this.documentService.resetAllowDuplicate();
    this.data.closeAllModal();
  }
}
