import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { ModalBaseComponent } from '../base/modal-base.component';
import { TranslateService } from '@ngx-translate/core';
import { MatSelectionList } from '@angular/material/list';

import { ConnectionService } from '../../../services/connection.service';
import { MessageService } from '../../../services/message.service';
import { GlobalsService } from '../../../services/globals.service';
import { WebintLinkService } from 'src/app/services/webint-link.service';
import { AddressService } from 'src/app/services/address.service';
import { HttpHeaders } from '@angular/common/http';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-create-ticket',
  templateUrl: './create-ticket.component.html',
  styleUrls: ['./create-ticket.component.scss'],
})
export class CreateTicketComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('crTicket', { static: true }) modalTemp: TemplateRef<any>;
  @ViewChild('tiTypes') selection: MatSelectionList;

  public ticketTypes: Record<string, string>[];
  public recent: string | null;

  private validTypes: string[];

  constructor(
    public translate: TranslateService,
    private connection: ConnectionService,
    private messageService: MessageService,
    private webintLink: WebintLinkService,
    private addressService: AddressService,
    private globals: GlobalsService,
    private connectionService: ConnectionService,
    private loader: LoaderService
  ) {
    super(translate);
    this.validTypes = [];
    this.ticketTypes = [];
    this.recent = sessionStorage.getItem('recentTicketType');
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.fetchTypes();
  }

  public confirmAction() {
    try {
      this.createTicket();
      console.log(this.globals.curMailAddr);
    } catch (e) {
      console.log(e);
      this.translate.get('LC53493').subscribe((text) => {
        this.messageService.showError(text);
      });
    }
  }

  private createTicket() {
    this.loader.activateLoader(true, true);
    const selectedValue = this.selection.selectedOptions.selected[0].value;

    // Split value components 0 = dbAlias, 1 = targetEmail
    const splitValueArr = selectedValue.split('#');

    console.log(this.addressService.currentAddressData);

    if (Array.isArray(splitValueArr) && splitValueArr.length > 1) {
      let pointer = '';
      if (this.addressService.currentAddressData) {
        pointer = this.addressService.currentAddressData.pointer;
      }

      this.createTicketHandler(
        this.globals.curMailUnid,
        splitValueArr[0],
        splitValueArr[1],
        pointer + '~#~' + this.globals.curMailAddr
      );
    }
  }

  // unid = Mail UNID
  // dbAlias = Service database alias
  // targetEMail = Service database ticket type specific mail-in address
  // pointer = addressPointer
  private createTicketHandler(
    unid: string,
    dbAlias: string,
    targetEMail: string,
    pointer: string,
    allowDuplicate?: boolean
  ) {
    // TODO: ist auf OWA abgestimmt, muss allgemeiner werden!
    const data = this.globals.curConnector.curPostData;
    this.loader.activateLoader(true, true);
    console.log(data);
    if (data != null) {
      // Ticket handling OWA
      if (this.globals.curConnectorType === 'Owa') {
        this.ticketHandlerOWA(
          data,
          unid,
          dbAlias,
          targetEMail,
          pointer,
          allowDuplicate
        );
      } else {
        this.ticketHandlerVerse(
          data,
          unid,
          dbAlias,
          targetEMail,
          pointer,
          allowDuplicate
        );
      }
    }
  }

  private ticketHandlerVerse(
    data: any,
    unid: string,
    dbAlias: string,
    targetEMail: string,
    pointer: string,
    allowDuplicate?: boolean
  ) {
    console.log('Ticket Verse');
    console.log(data);
    console.log(unid);
    console.log(dbAlias);
    console.log(targetEMail);
    console.log(pointer);

    if (allowDuplicate === true) {
      // Ignore
    } else {
      this.handleDuplicate(unid, dbAlias, targetEMail, pointer);
      return;
    }

    this.ticketPost(unid, dbAlias, targetEMail, pointer, allowDuplicate);
  }

  private ticketHandlerOWA(
    data: any,
    unid: string,
    dbAlias: string,
    targetEMail: string,
    pointer: string,
    allowDuplicate?: boolean
  ) {
    this.connectionService
      .postReq(
        'convert',
        'data=' + encodeURIComponent(JSON.stringify(data)),
        false,
        {
          headers: {
            'content-type': 'application/x-www-form-urlencoded',
          },
        }
      )
      .subscribe({
        next: (result: any) => {
          console.log('TICKET RESULT');
          console.log(result);
          unid = result;
          console.log(result);

          console.log(this.globals.curConnector);

          if (allowDuplicate === true) {
            // Ignore
          } else if (
            this.globals.curConnector.curPostData.officeAlreadyDoc ||
            this.globals.curConnector.curPostData.ticketAlreadyDoc
          ) {
            this.handleDuplicate(unid, dbAlias, targetEMail, pointer);
            return;
          }

          this.ticketPost(
            'OWA:' + unid,
            dbAlias,
            targetEMail,
            pointer,
            allowDuplicate
          );
        },
        error: (errHttp) => {
          this.loader.forceDeactivateLoader();
          this.messageService.showError(errHttp.statusText);
        },
        complete: () => {
          // Do nothing
        },
      });
  }

  private ticketPost(
    mailunid: string,
    dbalias: string,
    targetemail: string,
    addrpointer: string,
    allowDuplicate?: boolean
  ) {
    const queryObj: any = {
      mailunid,
      dbalias,
      targetemail,
      addrpointer,
    };

    if (allowDuplicate === true) {
      queryObj['allow-duplicate'] = true;
    }

    this.connection
      .postReq('ticket', this.connection.objectToHttpParams(queryObj))
      .subscribe({
        next: (resp: any) => {
          window.open(this.webintLink.convertToLink(resp.result), '_blank');
          this.globals.curConnector.alreadyDocumented = {
            officeAlreadyDoc:
              this.globals.curConnector.curPostData.officeAlreadyDoc,
            ticketAlreadyDoc: true,
          };
          this.loader.forceDeactivateLoader();
          this.translate.get('LC73236').subscribe((text) => {
            this.messageService.showSuccess(text);
          });
          this.closeAll();
        },
        error: (errHttp: any) => {
          if (errHttp.status === 409) {
            this.handleDuplicate(mailunid, dbalias, targetemail, addrpointer);
          } else {
            this.messageService.showError(errHttp.statusText);
          }
        },
        complete: () => {
          // Nothing
        },
      });
  }

  private handleDuplicate(
    unid: string,
    dbAlias: string,
    targetEMail: string,
    pointer: string
  ) {
    this.loader.forceDeactivateLoader();
    const duplicateModal = this.openAnotherModal('duplicate');
    if (duplicateModal) {
      const curSub = duplicateModal.afterClosed().subscribe((resp) => {
        if (resp) {
          this.loader.activateLoader(true, true);
          this.createTicketHandler(unid, dbAlias, targetEMail, pointer, resp);
        } else {
          curSub.unsubscribe();
        }
      });
    }
  }

  private fetchTypes() {
    this.connection.getReqModal('ticket', null).subscribe((resp) => {
      this.filterTypes(resp);
    });
  }

  private filterTypes(resp: any) {
    const ticketObj: any = {};
    if (Array.isArray(resp)) {
      for (const ticketOptionKey in resp) {
        if (
          Array.isArray(resp[ticketOptionKey]) &&
          resp[ticketOptionKey].length >= 2
        ) {
          const key = resp[ticketOptionKey][0];
          const value = resp[ticketOptionKey][1];
          ticketObj[key] = value;
          if (key !== 'giRecentSelection') {
            this.ticketTypes.push({
              key,
              value,
            });
          }
        }
      }
    }
    if (
      Object.keys(ticketObj).length === 0 &&
      ticketObj.constructor === Object
    ) {
      this.translate.get('LC73225').subscribe((text) => {
        this.messageService.showError(text);
      });
      return;
    }

    const ticketKeys = Object.keys(ticketObj);
    this.validTypes = Object.values(ticketObj);

    window.sessionStorage.setItem(
      'recentTicketType',
      ticketKeys[this.validTypes.indexOf(ticketObj.giRecentSelection)]
    );
  }
}
