import { Injectable } from '@angular/core';
import { MsalService } from '@azure/msal-angular';
import { Client } from '@microsoft/microsoft-graph-client';
import * as MicrosoftGraph from '@microsoft/microsoft-graph-types';

import { OAuthSettings } from '../data/oauth';
import { User } from '../data/user';
import { MessageService } from 'src/app/services/message.service';
import { GlobalsService } from 'src/app/services/globals.service';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  public authenticated: boolean;
  public user?: User;

  // <ConstructorSnippet>
  constructor(
    private msalService: MsalService,
    private messageService: MessageService,
    private globals: GlobalsService
  ) {
    /*this.authenticated = this.msalService.instance.getAllAccounts().length > 0;
    this.getUser().then((user) => {
      this.user = user;
    });*/
  }
  // </ConstructorSnippet>

  // Prompt the user to sign in and
  // grant consent to the requested permission scopes
  async signIn(skipTokenCheck?: boolean): Promise<void> {
    if (!skipTokenCheck) {
      const token = await this.getAccessToken();
      if (token && token !== '') {
        return;
      }
    }
    const result = await this.msalService
      .loginPopup(OAuthSettings)
      .toPromise()
      .catch((reason) => {
        this.globals.actionsAllowed = false;
        this.messageService.showError(
          this.globals.languageConfLocal.graph,
          'Microsoft Graph: Login failed',
          true
        );
      });

    if (result) {
      this.globals.actionsAllowed = true;
      this.globals.graphLoaded = true;
      this.authenticated = true;
      this.user = await this.getUser();
    }
  }

  // Sign out
  async signOut(): Promise<void> {
    await this.msalService.logout().toPromise();
    this.globals.graphLoaded = false;
    this.user = undefined;
    this.authenticated = false;
  }

  // Sign out Popup
  async signOutPopup(): Promise<void> {
    await this.msalService.logoutPopup().toPromise();
    this.user = undefined;
    this.authenticated = false;
  }

  // Silently request an access token
  async getAccessToken(): Promise<string> {
    const account = this.msalService.instance.getAllAccounts()[0];
    const result = await this.msalService
      .acquireTokenSilent({
        account: account ?? undefined,
        scopes: OAuthSettings.scopes,
      })
      .toPromise()
      .catch((reason) => {
        this.globals.actionsAllowed = false;
        this.messageService.showError(
          this.globals.languageConfLocal.graph,
          'Microsoft Graph: Get token failed. Try signing in.'
        );
        this.signIn(true);
      });

    if (result) {
      this.globals.graphLoaded = true;
      this.globals.actionsAllowed = true;
      return result.accessToken;
    }

    // Couldn't get a token
    this.authenticated = false;
    return '';
  }

  // <GetUserSnippet>
  private async getUser(): Promise<User | undefined> {
    if (!this.authenticated) {
      return undefined;
    }

    const graphClient = Client.init({
      // Initialize the Graph client with an auth
      // provider that requests the token from the
      // auth service
      authProvider: async (done) => {
        const token = await this.getAccessToken().catch((reason) => {
          done(reason, null);
        });

        if (token) {
          done(null, token);
        } else {
          done('Could not get an access token', null);
        }
      },
    });

    // Get the user from Graph (GET /me)
    const graphUser: MicrosoftGraph.User = await graphClient
      .api('/me')
      .select('displayName,mail,mailboxSettings,userPrincipalName')
      .get();

    const user = new User();
    user.displayName = graphUser.displayName ?? '';
    // Prefer the mail property, but fall back to userPrincipalName
    user.email = graphUser.mail ?? graphUser.userPrincipalName ?? '';
    user.timeZone = graphUser.mailboxSettings?.timeZone ?? 'UTC';

    // Use default avatar
    user.avatar = '/assets/no-profile-photo.png';

    return user;
  }
  // </GetUserSnippet>
}
