import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';

import { ModalBaseComponent } from '../base/modal-base.component';
import { TranslateService } from '@ngx-translate/core';
import { MatSelectionList } from '@angular/material/list';

import { ConnectionService } from '../../../services/connection.service';
import { MessageService } from '../../../services/message.service';

@Component({
  selector: 'app-select-contact',
  templateUrl: './select-contact.component.html',
  styleUrls: ['./select-contact.component.scss'],
})
export class SelectContactComponent
  extends ModalBaseComponent
  implements OnInit
{
  @ViewChild('selContact', { static: true }) modalTemp: TemplateRef<any>;
  @ViewChild('dbs') selection: MatSelectionList;

  public contactDBs: Record<string, string>[];

  constructor(
    public translate: TranslateService,
    private connection: ConnectionService,
    private messageService: MessageService
  ) {
    super(translate);
    this.contactDBs = [];
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.fetchContactsDBs();
  }

  public confirmAction(selection?: any, preselectDB?: any) {
    try {
      let chosenDB;
      if (preselectDB != null) {
        chosenDB = preselectDB;
      } else {
        chosenDB = this.selection.selectedOptions.selected[0].value;
      }
      this.openAnotherModal(this.next, {
        contactsDB: chosenDB,
        createType: this.data.createType,
      });
    } catch (e) {
      this.translate.get('LC53493').subscribe((text) => {
        this.messageService.showError(text);
      });
    }
  }

  private fetchContactsDBs() {
    this.connection.getReqModal('dbConnections', null).subscribe((resp) => {
      this.clearOfficeDBs();
      this.extractContacts(resp);
    });
  }

  private async extractContacts(resp: any) {
    for (const db in resp) {
      if (Object.prototype.hasOwnProperty.call(resp, db)) {
        if (db === 'GeOfficeAliases') {
          continue;
        }
        if (resp[db].type === 'Contact') {
          console.log(resp);
          this.contactDBs.push({
            title: resp[db].title,
            db,
          });
        }
      }
    }

    if (this.contactDBs.length < 1) {
      this.translate.get('LC73225').subscribe((text) => {
        this.messageService.showError(text);
        this.cancelAction();
        return;
      });
    }

    if (this.contactDBs.length === 1) {
      this.confirmAction(this.selection, this.contactDBs[0].db);
    }
  }

  private clearOfficeDBs() {
    this.contactDBs.length = 0;
  }
}
