import { Component, OnInit } from '@angular/core';
import { LanguageService } from '../../services/language.service';
import { GlobalsService } from '../../services/globals.service';
import { ModalService } from '../../services/modal.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalType } from 'src/app/types/modal.type';
import { AddressService } from 'src/app/services/address.service';

@Component({
  selector: 'app-additional-actions',
  templateUrl: './additional-actions.component.html',
  styleUrls: ['./additional-actions.component.scss'],
})
export class AdditionalActionsComponent implements OnInit {
  public bmIcons: { [klass: string]: any } | null;

  public translations: Record<number, string>;

  constructor(
    public language: LanguageService,
    public globals: GlobalsService,
    public modalService: ModalService,
    public translate: TranslateService,
    public addressService: AddressService
  ) {
    this.translations = {};
    this.bmIcons = {
      background:
        'transparent no-repeat url(assets/images/icons_business_mail.png)',
    };

    translate.setDefaultLang('all');
    translate.use('all');
  }

  ngOnInit(): void {}

  public openModal(newType: ModalType) {
    this.modalService.changeType({
      newType,
    });
  }
}
