import { Injectable } from '@angular/core';
import { Subscriber } from 'rxjs';
import { WaitListFuncType } from '../types/waitlist-function.model';
import { WaitListType, waitListTypes } from '../types/waitlist.type';

@Injectable({
  providedIn: 'root',
})
export class WaitListService {
  private waitListRecord: Record<WaitListType, Subscriber<any>[]>;
  private waitListWithFunction: Record<WaitListType, WaitListFuncType[]>;

  constructor() {
    this.waitListRecord = {} as Record<WaitListType, Subscriber<any>[]>;
    this.waitListWithFunction = {} as Record<WaitListType, WaitListFuncType[]>;
    console.log(waitListTypes);
    waitListTypes.forEach((element: WaitListType) => {
      this.waitListRecord[element] = [];
      this.waitListWithFunction[element] = [];
    });
    console.log(this.waitListRecord);
  }

  public addToWaitList(waitListName: WaitListType, observer: Subscriber<any>) {
    this.waitListRecord[waitListName].push(observer);
  }

  public addToWaitListFunc(
    waitListName: WaitListType,
    observer: Subscriber<any>,
    func: any
  ) {
    this.waitListWithFunction[waitListName].push({
      subscriber: observer,
      function: func,
    });
  }

  public accessWaitList(waitListName: WaitListType) {
    return this.waitListRecord[waitListName];
  }

  public clearWaitList(waitListName: WaitListType) {
    this.waitListRecord[waitListName].length = 0;
  }

  public resolveFuncWaitList(waitListName: WaitListType) {
    for (const entry of this.waitListWithFunction[waitListName]) {
      entry.function(entry.subscriber);
    }
    this.waitListWithFunction[waitListName].length = 0;
  }
}
