import { Component, OnDestroy, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';

import { ActivitiesService } from '../../services/activities.service';
import { ImageService } from '../../services/image.service';
import { MessageService } from '../../services/message.service';
import { WebintLinkService } from '../../services/webint-link.service';

import { Activity } from '../../types/activity.model';
import { ActivityEntry } from '../../types/activity-entry.model';
import { ModalService } from 'src/app/services/modal.service';
import { AddressService } from 'src/app/services/address.service';
import { TranslateService } from '@ngx-translate/core';
import { GlobalsService } from 'src/app/services/globals.service';

const MAX_ACTIVITIES = 10;

@Component({
  selector: 'app-activities',
  templateUrl: './activities.component.html',
  styleUrls: ['./activities.component.scss'],
})
export class ActivitiesComponent implements OnInit, OnDestroy {
  public activity: Activity;
  public types: string[];
  public selectedType: string;
  public activityDocImage: { [klass: string]: any } | null;
  public selectArrow: { [klass: string]: any } | null;
  public headerText: string;

  private typesSet: boolean;

  constructor(
    private imageService: ImageService,
    private activitiesService: ActivitiesService,
    private messageService: MessageService,
    private webintLinkService: WebintLinkService,
    private modalService: ModalService,
    private addressService: AddressService,
    public globals: GlobalsService,
    public translate: TranslateService
  ) {
    this.activity = {} as Activity;
    this.types = [];
    this.typesSet = false;
    this.selectedType = '0';
    this.activityDocImage = {
      background: 'no-repeat url(assets/images/icons_business_mail.png)',
    };
    this.selectArrow = {
      background:
        'no-repeat url(assets/images/DropDownArrowOffsetRight.png) right',
    };
    this.headerText = '';
  }

  ngOnInit(): void {
    console.log('init Activities');
    this.init();
    this.addressService.waitForAddressChange().subscribe(() => {
      this.activity = {} as Activity;
      this.init();
    });
  }

  ngOnDestroy(): void {
    this.activitiesService.destroy();
  }

  public document(pointer: string) {
    this.modalService.changeType({
      newType: 'documentFile',
      data: { currentOfficePointer: pointer },
    });
  }

  public trackPointer(index: number, item: ActivityEntry) {
    return item.pointer;
  }

  public createWebintLink(pointer: string) {
    return this.webintLinkService.convertToLink(pointer);
  }

  public onOptionChange(typeValue: string) {
    this.activitiesService.changeDefaultTypeSelection(typeValue);
  }

  private init() {
    this.activitiesService.init().subscribe({
      next: (resp: any) => {
        this.translate.get('LC52160').subscribe((text: string) => {
          this.headerText = text.replace('<max>', String(MAX_ACTIVITIES));
        });
        switch (resp.type) {
          case 'activities':
            this.activity = resp;
            if (this.activity.displayType) {
              this.selectedType = this.activity.displayType.toString(10);
            }
            console.log(this.activity);
            this.transformForDisplay();
            if (this.activity.errorCode && this.activity.message) {
              this.messageService.showInfo(this.activity.message);
            }
            break;
          case 'types':
            if (!this.typesSet) {
              this.types = resp;
            }
            break;
          default:
            if (this.activity.errorCode && this.activity.message) {
              this.messageService.showError(this.activity.message);
            }
        }
      },
      error: (errHttp: any) => {
        console.log('Activities failure!');
        console.log(errHttp);
      },
      complete: () => {
        // Nothing
      },
    });
  }

  private transformForDisplay() {
    if (this.activity.activities !== []) {
      this.activity.activities.forEach((entry) => {
        entry.viewIconUrl = this.imageService.getViewIconUrl(
          parseInt(entry.viewIcon, 10)
        );
        entry.dateString = formatDate(entry.dateInMS, 'dd.MM.YYYY', 'en_US');
      });
    }
  }
}
