import { Pipe, PipeTransform } from '@angular/core';
import { spacerIndices, order } from '../types/address-order.display';

@Pipe({
  name: 'addSpacer',
})
export class AddSpacerPipe implements PipeTransform {
  transform(value: any, ...args: unknown[]): any {
    console.log('PIPE!!!!!!!!!!!!!!!!');
    console.log(typeof value);
    return this.addSpacerToObject(value);
  }

  // Checks order of AddressOrder and adds -BLANK- placeholder to displayable object
  private addSpacerToObject(obj: Record<string, any>[]) {
    const orderIndices: number[] = [];
    const objWithSpacers: Record<string, any>[] = [];

    for (const currentEntry of obj) {
      orderIndices.push(order.indexOf(currentEntry.key));
    }

    let curEntryIndex = 0;
    let curSpacerIndex = 0;

    for (const curOrderIndex of orderIndices) {
      if (curSpacerIndex < spacerIndices.length) {
        if (curOrderIndex > spacerIndices[curSpacerIndex]) {
          if (
            curEntryIndex > 0 &&
            objWithSpacers[curEntryIndex - 1].key === '-BLANK-'
          ) {
            // Skip Spacer
          } else {
            objWithSpacers.push({
              key: '-BLANK-',
              value: '-BLANK-',
            });
          }
          curSpacerIndex++;
        }
      }
      objWithSpacers.push(obj[curEntryIndex]);
      curEntryIndex++;
    }

    return objWithSpacers;
  }
}
