import { ModalType } from '../types/modal.type';

export default class CreateAddressItems {
  public items = [
    {
      name: 'createAddressCompany' as ModalType,
      lc: 'LC73000',
    },
    {
      name: 'createAddressToCompany' as ModalType,
      lc: 'LC73001',
    },
    {
      name: 'createAddressPerson' as ModalType,
      lc: 'LC73003',
    },
  ];

  public addNewItem(name: ModalType, lc: string) {
    this.items.push({
      name,
      lc,
    });
  }
}
