import { Component, OnInit } from '@angular/core';
import { CreateAddressService } from 'src/app/services/create-address.service';
import { GlobalsService } from 'src/app/services/globals.service';
import { ModalService } from 'src/app/services/modal.service';
import { CreateAddressOverview } from 'src/app/types/create-address-overview';
import { ModalType } from 'src/app/types/modal.type';

@Component({
  selector: 'app-create-address-block',
  templateUrl: './create-address-block.component.html',
  styleUrls: ['./create-address-block.component.scss'],
})
export class CreateAddressBlockComponent implements OnInit {
  public isOpen: boolean;
  public items: CreateAddressOverview[];
  public createPlusImage: { [klass: string]: any };

  constructor(
    private createService: CreateAddressService,
    private modalService: ModalService,
    public globals: GlobalsService
  ) {
    this.createPlusImage = {
      background:
        'transparent no-repeat url(' + globals.webInt + 'icons_general.png)',
    };
  }

  ngOnInit(): void {
    this.connectToService();
  }

  public update(newVal: boolean) {
    this.isOpen = newVal;
  }

  public showModal(val: ModalType) {
    this.modalService.changeType({
      newType: val,
    });
  }

  private connectToService() {
    this.createService.connect().subscribe((resp) => {
      this.update(resp);
      this.items = this.createService.items;
    });
  }
}
