import { Injectable } from '@angular/core';
import { Observable, Subscriber } from 'rxjs';
import { GlobalsService } from './globals.service';
import { InitService } from './init.service';
import { MessageService } from './message.service';

@Injectable({
  providedIn: 'root',
})
export class LoaderService {
  private loaderCompSubscriber: Subscriber<any>;
  private isInit: boolean;
  private initServiceInit: boolean;
  private forceOpen: boolean;
  private loaderTimeout: NodeJS.Timeout | null;
  private isActive: boolean;

  constructor(
    private init: InitService,
    private messageService: MessageService,
    private globals: GlobalsService
  ) {
    this.isInit = false;
    this.initServiceInit = false;
    this.init.waitForInit().subscribe((resp) => {
      this.initServiceInit = resp;
      this.deactivateLoader();
    });
    this.forceOpen = false;
    this.loaderTimeout = null;
    this.isActive = false;
  }

  deactivateLoaderAfterTimeout() {
    if (this.isActive) {
      this.loaderTimeout = setTimeout(() => {
        this.forceDeactivateLoader();
      }, 5000);
    }
  }

  activateLoader(isModal: boolean, forceOpen?: boolean) {
    if (!this.globals.loginOpen) {
      this.clearTimeout();
      this.deactivateLoaderAfterTimeout();
    }
    if (forceOpen) {
      this.isActive = true;
      this.forceOpen = forceOpen;
    }
    if (this.isInit) {
      this.isActive = true;
      this.loaderCompSubscriber.next({
        activate: true,
        modal: isModal,
      });
    }
  }

  clearTimeout() {
    this.messageService.clearConnectionTimeout();
    if (this.loaderTimeout) {
      clearTimeout(this.loaderTimeout);
      this.loaderTimeout = null;
    }
  }

  deactivateLoader() {
    if (this.isInit && this.initServiceInit && !this.forceOpen) {
      this.isActive = false;
      if (!this.globals.loginOpen) {
        this.clearTimeout();
      }
      this.messageService.clearConnectionTimeout();
      this.loaderCompSubscriber.next({
        activate: false,
      });
    }
  }

  forceDeactivateLoader() {
    this.isActive = false;
    if (!this.globals.loginOpen) {
      this.clearTimeout();
    }
    this.forceOpen = false;
    this.loaderCompSubscriber.next({
      activate: false,
    });
  }

  connectToService() {
    return new Observable<any>((observer) => {
      this.loaderCompSubscriber = observer;
      this.isInit = true;
    });
  }
}
